import { createSlice } from '@reduxjs/toolkit'

const stageSlice = createSlice({
	name: 'stage',
	initialState: {
		stage: {},
		immutableState: [],
		playersList: [],
		chosenPlayerInList: {},
		dragState: {},
		currentStageNumber: 1,
	},
	reducers: {
		setStageData(state, action) {
			state.stage = action.payload
		},
		setImmutableState(state, action) {
			state.immutableState = action.payload
		},
		addParticipant(state, action) {
			const { levelIndex, groupIndex, node } = action.payload
			state.stage.levels[levelIndex].groups[groupIndex].nodes.push(node)
		},
		dragParticipant(state, action) {
			const {
				sourceLevelIndex,
				endLevelIndex,
				endGroupIndex,
				sourceGroupIndex,
				endIndex,
				sourceIndex,
				movedNode,
				replacedNode
			} = action.payload

			state.stage.levels[sourceLevelIndex].groups[sourceGroupIndex].nodes[sourceIndex] = replacedNode
			state.stage.levels[endLevelIndex].groups[endGroupIndex].nodes[endIndex] = movedNode
		},
		replaceParticipant(state, action) {
			state.stage.levels = action.payload
		},
		updateParticipantNumber(state, action) {
			const { levelIndex, groupIndex, number } = action.payload
			state.stage.levels[levelIndex].groups[groupIndex].participants_count = number
		},
		updateGroupNodes(state, action) {
			const { levels} = action.payload
			state.stage.levels = levels
		},
		updateGroupName(state, action) {
			const { levelIndex, groupIndex, groupName } = action.payload
			state.stage.levels[levelIndex].groups[groupIndex].name = groupName
		},
		addLevel(state, action) {
			state.stage.levels.push(action.payload)
		},
		removeLevel(state, action) {
			state.stage.levels.splice(action.payload, 1)
		},
		addGroup(state, action) {
			const { levelIndex, group } = action.payload
			state.stage.levels[levelIndex].groups.push(group)
		},
		removeGroup(state, action) {
			const { levelIndex, groupIndex } = action.payload
			state.stage.levels[levelIndex].groups.splice(groupIndex, 1)
		},
		setReplacedPlayer(state, action) {
			state.replacedPlayer = action.payload
		},
		setPlayersList(state, action) {
			state.playersList = action.payload
		},
		setChosenPlayerInList(state, action) {
			state.chosenPlayerInList = action.payload
		},
		setDragState(state, action) {
			state.dragState = action.payload
		},
		setCurrentStageNumber(state, action) {
			state.currentStageNumber = action.payload
		}
	}
})

export const {
	setStageData,
	addParticipant,
	updateGroupNodes,
	updateGroupName,
	updateParticipantNumber,
	addLevel,
	addGroup,
	removeLevel,
	removeGroup,
	dragParticipant,
	replaceParticipant,
	setStageLevels,
	setImmutableState,
	setPlayersList,
	setChosenPlayerInList,
	setDragState,
	setCurrentStageNumber
} = stageSlice.actions
export default stageSlice.reducer