import { createSlice } from '@reduxjs/toolkit'

import { getLocalStorageItem, setItemInLocalStorage } from '../../utils/functions'

function getCommunities() {
	try{
		const obj = getLocalStorageItem('communities')
		const parsedObj = obj ? JSON.parse(obj) : []
		return parsedObj.obj
	} catch (e) {
		console.log('Get communitites error' , e)
	}
}

const communitiesSlice = createSlice({
	name: 'communities',
	initialState: {
		communities: getCommunities()
	},
	reducers: {
		setCommunities(state, action) {
			const comunitites = JSON.stringify(action.payload)
			setItemInLocalStorage('communities', comunitites)
			state.communities = action.payload
		}
	}
})

export const {
	setCommunities,
} = communitiesSlice.actions
export default communitiesSlice.reducer