import { createSlice } from '@reduxjs/toolkit'

const tournamentParticipantsSlice = createSlice({
	name: 'participants',
	initialState: {
		participants: []
	},
	reducers: {
		setParticipants(state, action) {
			const { participants } = action.payload
			state.participants = participants
		},
		addParticipant(state, action) {
			const { participant } = action.payload
			const updated = [...state.participants, participant]
			state.participants = updated
		},
		updateParticipant(state, action) {
			state.participants = state.participants.map(p => {
				if (p?.uid === action.payload.uid) {
					return p = action.payload
				} else {
					return p
				}
			})
		}
	}
})

export const { setParticipants, addParticipant, updateParticipant } = tournamentParticipantsSlice.actions
export default tournamentParticipantsSlice.reducer