import { useTranslation } from 'react-i18next'
import { useParams } from 'react-router-dom'

import './PlayerInfo.scss'
import { useGetPlayerQuery } from '../../redux/playersService/playersApiSlice'
import Header from '../../reusableComponents/Header/Header'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu'
import LabelBlock from '../../reusableComponents/LabelBlock/LabelBlock'
import Loader from '../../reusableComponents/Loader/Loader'

function PlayerInfo() {
	const { t } = useTranslation()
	const { uid } = useParams()
	const { data = [], isLoading } = useGetPlayerQuery({ uid })

	return (
		<>
			<Header
				title={t('Player')}
				edit
				editUrl={`/player/form/${uid}`}
				back
				backUrl={'/players'}
				border
			/>

			<main className="player-info">
				{
					isLoading ?
						<Loader />
						:
						<>
							<LabelBlock
								borderBottom
								label={t('Last name')}
								content={data.last_name}
							/>

							<LabelBlock
								borderBottom
								label={t('First name')}
								content={data.first_name}
							/>

							<LabelBlock
								borderBottom
								label={t('Middle name')}
								content={data.middle_name || '___'}
							/>

							<LabelBlock
								borderBottom
								label={t('Birth date')}
								content={data.birth_date || '___'}
							/>

							<LabelBlock
								borderBottom
								label={t('Email')}
								content={data.email || '___'}
							/>

							<LabelBlock
								borderBottom
								label={t('Phone number')}
								content={data.mobile || '___'}
							/>
						</>
				}
			</main>

			<BottomMenu />
		</>
	)
}

export default PlayerInfo