import { useNavigate } from 'react-router-dom'
import { useEffect, useCallback } from 'react'

import { useGetCommunitiesQuery } from '../../redux/communitiesService/communitiesApiSlice'

import LocalStorageManager from '../../LocalStorageManager'

const Communities = () => {
	const navigate = useNavigate()
	const { data: communities = [], error, isLoading } = useGetCommunitiesQuery()

	const goToCommunity = useCallback((community) => {
		LocalStorageManager.setItem('communityUid', community?.uid)
		LocalStorageManager.setItem('communityName', community?.name)
		navigate('/tournaments')
	}, [navigate])

	useEffect(() => {
		if (communities?.length > 0 && !isLoading && !error) {
			LocalStorageManager.setItem('communities', communities)
			goToCommunity(communities?.[0])
		}
	}, [communities, isLoading, error, goToCommunity])

	if (error) { console.log(error) }
	if (isLoading) { return <h1>Loading</h1> }

	return (
		<>
		</>
	)
}

export default Communities
