import { memo, useCallback, useMemo } from 'react'
import { useParams, useNavigate, useLocation } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'

import './StagesMenu.scss'
import { ReactComponent as PenIcon } from '../../icons/pen_stage.svg'
import { ReactComponent as MagicStickIcon } from '../../icons/magic_stick.svg'
import { selectAuth, selectTournament } from '../../redux/selectors'
import { getParameters } from '../../utils/functions'
import { setCurrentStageNumber } from '../../redux/stageService/stageSlice'
import { useCreateStageMutation } from '../../redux/stageService/stageApiSlice'

function StagesMenu({ structure }) {
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const location = useLocation()
	const { authorized } = useSelector(selectAuth)
	const [generateStage] = useCreateStageMutation()
	// *: "structure/1"
	// stageNumber: "1"
	// tournamentParams: "etdsc2"
	const { tournamentParams, stageNumber } = useParams()
	// guest: null
	// tournamentUid: "etdsc2"
	const { tournamentUid } = getParameters(tournamentParams)
	// auth_code: "6xJIK7J"
	// category_uid: null
	// comment: null
	// community_uid: "akdmfp"
	// description: ""
	// doubles: [{…}, {…}, {…}, {…}, {…}, {…}, {…}, {…}, {…}, {…}, {…}, {…}]
	// gender: null
	// name: "Турнир 2253"
	// participants_count: 12
	// players: null
	// round_robin_ranking_system: "group_sets_games_win_percentage"
	// stages_count: 0
	// start_date: "2025-04-05"
	// status: "not_yet_started"
	// tier: "tier1"
	// type: "doubles"
	// uid: "etdsc2"
	// venue_uid: "61c2oo"
	const { stages_count } = useSelector(selectTournament)

	const stages = useMemo(() => {
		return Array.from({ length: stages_count }, (_, index) => index + 1)
	}, [stages_count])

	const handleStageTab = useCallback((number) => {
		dispatch(setCurrentStageNumber(number))

		const pathParts = location.pathname.split('/')
		navigate(`/${pathParts[1]}/${pathParts[2]}/${number}`)
	}, [location.pathname, navigate, dispatch])

	const handleAddStage = useCallback(async () => {
		try {
			const response = await generateStage({ tournament_uid: tournamentUid }).unwrap()
			handleStageTab(response.order_number)
		}
		catch (e) {
			console.log('authomatical creation in stageCreation catch error', e)
		}
	}, [tournamentUid, generateStage, handleStageTab])

	function checkStageNumber(number) {
		const tab = Number(stageNumber)

		return tab === number
	}

	function handleStick() {
		navigate(`/${tournamentUid}/structure/generation/${stageNumber}`)
	}

	function handlePen() {
		navigate(`/${tournamentUid}/structure/${stageNumber}`)
	}

	function getPatch() {
		const patch = location.pathname.split('/')

		if (patch.length === 4 && patch[2] === 'structure') {
			return 1
		} else if (patch.length === 5 && patch[2] === 'edit' && patch[3] === 'structure') {
			return 2
		} else if (patch.length === 5 && patch[2] === 'structure' && patch[3] === 'generation') {
			return 3
		} else {
			return 0
		}
	}

	return (
		<div className="stages-menu">
			{
				!structure ?
					<div className="stages-menu__wrapper">
						{
							stages.length > 0 && stages.map((s, index) => (
								<button
									type="button"
									onClick={() => handleStageTab(index + 1)}
									className={
										!checkStageNumber(index + 1) ?
											'stages-menu__button--inactive'
											:
											'stages-menu__button--active'
									}
									key={index}
								>
									{index + 1}
								</button>
							))
						}
					</div>
					:
					<div className="stages-menu__wrapper">
						{
							stages.length > 0 && stages.map((s, index) => (
								<div
									key={index}
									className={
										!checkStageNumber(index + 1) ?
											''
											:
											'stages-menu__item--active'
									}
								>
									{
										!checkStageNumber(index + 1) ?
											<button
												type="button"
												onClick={() => handleStageTab(index + 1)}
												className="stages-menu__button--inactive"
											>
												{index + 1}
											</button>
											:
											<div className="stages-menu__item-options">
												<button
													type="button"
													onClick={() => handleStageTab(index + 1)}
													className="stages-menu__item-options-button"
												>
													{index + 1}
												</button>

												<PenIcon
													onClick={handlePen}
													className={
														getPatch() === 1 ?
															'stages-menu__item-options-icon--active'
															:
															'stages-menu__item-options-icon'
													}
												/>

												<MagicStickIcon
													onClick={handleStick}
													className={
														getPatch() === 3 ?
															'stages-menu__item-options-icon--active'
															:
															'stages-menu__item-options-icon'
													}
												/>
											</div>
									}
								</div>
							))
						}
					</div>
			}

			{
				stages_count < 2 && authorized &&
				<div className="stages-menu__block">
					<button
						type="button"
						onClick={handleAddStage}
						className="stages-menu__block-button-add"
					>
						+
					</button>
				</div>
			}
		</div>
	)
}

export default memo(StagesMenu)