import { TYPE_SINGLES, TYPE_DOUBLES, NODE_TYPE_OUT, NODE_TYPE_MIDDLE } from '../../utils/constants'

import { getNewUid } from '../../utils/functions'

export const formGroupsParticipants = (nodes, players, type) => {
	return nodes?.filter(node => node?.type !== NODE_TYPE_OUT || node?.type !== NODE_TYPE_MIDDLE)
		.map((node) => {
			if (type === TYPE_SINGLES) {
				const participant = players?.find((p) => p?.uid === node?.player_uid)

				return {
					nodeUid: participant?.uid,
					orderNumber: node?.order_number,
					name: `${participant?.last_name} ${participant?.first_name}`
				}
			} else if (type === TYPE_DOUBLES) {
				const participant = players?.find((p) => p?.uid === node?.double_uid)

				return {
					nodeUid: participant?.uid,
					orderNumber: node?.order_number,
					firstPlayerNode: participant?.player1?.uid,
					secondPlayerNode: participant?.player1?.uid,
					firstPlayerName: `${participant?.player1?.last_name} ${participant?.player1?.first_name}`,
					secondPlayerName: `${participant?.player2?.last_name} ${participant?.player2?.first_name}`
				}
			}
		})
}

export const addEmptyPlayerInList = (nodesList) => {
	const updatedList = [...nodesList]

	updatedList.unshift({
		placed: false,
		name: '---',
		nodeUid: getNewUid(),
		orderNumber: 0,
		playerUid: null,
		doubleUid: null,
		rating: '-',
		emptyPlayer: true
	})

	return updatedList
}

export const existingMatches = (grp) => {
	if (grp) {
		for (const m of grp.matches || []) {
			if (m && m.sets && m.sets.length > 0) {
				return true
			}
		}
	}
	return false
}