import { createSlice } from '@reduxjs/toolkit'

const matchSlice = createSlice({
	name: 'match',
	initialState: {
		match: {}
	},
	reducers: {
		setMatchData(state, action) {
			state.match = action.payload
		}
	}
})

export const {
	setMatchData
} = matchSlice.actions
export default matchSlice.reducer