import { memo } from 'react'

import './RoundedButton.scss'

function RoundedButton({ title, background, onClick, type, marginBottom, border, disabled }) {
	const WHITE = 'white'
	const GREY = 'grey'
	const DARK_GREY_COLOR = 'var(--palette-live-grey-0)'
	const WHITE_COLOR = 'var(--palette-live-bg-1-white-old)'
	const DISABLED_TEXT_COLOR = 'var(--palette-live-grey-9)'
	const LIGHT_GREY_COLOR = 'var(--palette-live-grey-11)'
	const BORDER_STYLE = '1px solid ' + DARK_GREY_COLOR

	return (
		<button
			className="rounded-button"
			style={{
				backgroundColor: background === WHITE ?
					WHITE_COLOR
					:
					background === GREY || disabled ? LIGHT_GREY_COLOR : DARK_GREY_COLOR,
				color: background === WHITE || (background === GREY && !disabled) ?
					DARK_GREY_COLOR
					:
					!disabled ? WHITE_COLOR : DISABLED_TEXT_COLOR,
				marginBottom: marginBottom ? '7px' : '',
				border: border && !disabled ? BORDER_STYLE : 'none'
			}}
			onClick={disabled ? undefined : onClick}
			type={type}
		>
			{ title }
		</button>
	)
}

export default memo(RoundedButton)