import React, { useState, useEffect, useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector, useDispatch } from 'react-redux'
import { useLocation, useNavigate, useParams } from 'react-router'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'

import './RoundRobinGrid.scss'
import { getParameters } from '../../utils/functions'
import { selectTournament } from '../../redux/selectors'
import { setMatchData } from '../../redux/matchService/matchSlice'
import Popup from '../../reusableComponents/Popup/Popup'
import { TYPE_SINGLES, NODE_TYPE_IN, RR_RANKING_SYST_EQUAL } from '../../utils/constants'
import { ReactComponent as Ball } from '../../icons/tennis_ball.svg'
import { ReactComponent as DND } from '../../icons/dnd.svg'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'

import { getColumnsWithSameResults, convertInPercent, includeInDisput } from './externalFunctions'

function RoundRobinGrid({ group, auth }) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const location = useLocation()
	const dispatch = useDispatch()
	const { tournamentParams } = useParams()
	const { tournamentUid } = getParameters(tournamentParams)
	const { type, players, doubles, round_robin_ranking_system } = useSelector(selectTournament)
	// Получаем объединенный массив для таблицы Сетка
	const [grid, setGrid] = useState([])
	const [gridResults, setGridResults] = useState([])
	const [openResultEditor, setOpenResultEditor] = useState(false)
	const [popupList, setPopupList] = useState([])

	// useEffect для таблицы Сетка
	useEffect(() => {
		if (Object.keys(group).length !== 0) {
			// Получаем имена игроков по группам
			const gridArr = group.nodes.flatMap(node => {
				let playerObject

				if (node.type === NODE_TYPE_IN) {
					if (node.double_uid === null && node.player_uid === null) {
						// Если нет double_uid и player_uid
						return node
					} else if (players !== null) {
						// Одиночный
						playerObject = players.find(player => player.uid === node.player_uid)
					} else {
						// Парный
						playerObject = doubles.find(doublesPlayer => doublesPlayer.uid === node.double_uid)
					}

					if (playerObject) {
						// Возвращаем объект с установленным nodeUid
						return {
							...playerObject,
							nodeUid: node?.uid
						}
					}
				}

				return null // Возвращаем null для элементов, которые не прошли фильтр
			}).filter(player => player !== null)

			// Получаем все результаты игр по группам
			const gridResultArr = group.results.map((result) => {
				return result
			})

			setGrid(gridArr)
			setPopupList(gridArr)
			setGridResults(gridResultArr)
		}
		else {
			return
		}
	}, [doubles, players, group])

	// Возвращаем матчи и инвертируем их по необходимости
	function getMatchPlusInverted(sideOne, sideTwo, indexOne, indexTwo) {
		const matches = group.matches
		let inverted = Boolean
		let foundMatch = []
		const sideOneUid = grid[indexOne].nodeUid || grid[indexOne].sideOne?.uid
		const sideTwoUid = grid[indexTwo].nodeUid || grid[indexTwo].sideTwo?.uid

		foundMatch = matches.filter(match =>
			match.side1_uid === sideOneUid && match.side2_uid === sideTwoUid
		)

		if (foundMatch.length === 0) {
			// Если ничего не нашли, то мы проверям повторно, но инвертировав index1 и index2
			foundMatch = matches.filter(match =>
				match.side2_uid === sideOneUid && match.side1_uid === sideTwoUid
			)

			if (foundMatch.length !== 0) {
				// Если мы инвертировав нашли нужный нам элемент, то указываем в inverted = true
				inverted = true
			} else {
				// Если не смогли получить сеты игроков, то вызываем функцию goToMatch которая возвращает 0:0
				inverted = false

				return [
					matches,
					inverted
				]
			}
		} else {
			// Если нашли, то указываем в inverted = false
			inverted = false
		}

		return [
			foundMatch,
			inverted
		]
	}

	function defineAvailablityOfPlayer(player) {
		if (type === TYPE_SINGLES) {
			return {
				...player,
				available: player?.formedName ? true : false
			}
		} else {
			return {
				...player,
				available: player?.player1 ? true : false
			}
		}
	}

	// Вызываем внутри таблицы для получения матчей
	function getMatch(playerOne, playerTwo, indexOne, indexTwo) {
		const updatedPlayerOne = defineAvailablityOfPlayer(playerOne)
		const updatedPlayerTwo = defineAvailablityOfPlayer(playerTwo)

		const [matches, inverted] = getMatchPlusInverted(playerOne, playerTwo, indexOne, indexTwo)
		return setString(matches, inverted, updatedPlayerOne, updatedPlayerTwo)
	}

	function formMatch(match, participantOne, participantTwo, inverted) {
		let matchWithPlayers = {}

		const invertedMatch = {
			...match[0],
			sets: match[0].sets.map(set => {
				return {
					...set,
					score1: inverted ? set.score2 : set.score1,
					score2: inverted ? set.score1 : set.score2,
					tie_break_score1: inverted ? set.tie_break_score2 : set.tie_break_score1,
					tie_break_score2: inverted ? set.tie_break_score1 : set.tie_break_score2,
				}
			})
		}

		if (type === TYPE_SINGLES) {
			matchWithPlayers = {
				...invertedMatch,
				side1: {
					namePlayer: participantOne.formedName,
					nodeUid: inverted ? participantTwo.nodeUid : participantOne.nodeUid
				},
				side2: {
					namePlayer: participantTwo.formedName,
					nodeUid: inverted ? participantOne.nodeUid : participantTwo.nodeUid
				}
			}
		} else {
			matchWithPlayers = {
				...invertedMatch,
				side1: {
					namePlayer1: participantOne?.player1?.formedName,
					namePlayer2: participantOne?.player2?.formedName,
					nodeUid: inverted ? participantTwo.nodeUid : participantOne.nodeUid
				},
				side2: {
					namePlayer1: participantTwo?.player1?.formedName,
					namePlayer2: participantTwo?.player2?.formedName,
					nodeUid: inverted ? participantOne.nodeUid : participantTwo.nodeUid
				}
			}
		}

		return {
			...matchWithPlayers,
			inverted
		}
	}

	function openMatch(match, playerOne, playerTwo, inverted) {
		if (!auth) {
			return
		}

		if (!playerOne?.available || !playerTwo?.available) {
			return
		}

		const matchWithPlayers = formMatch(match, playerOne, playerTwo, inverted)
		matchWithPlayers.pathBack = location.pathname
		dispatch(setMatchData(matchWithPlayers))
		sessionStorage.setItem('match', JSON.stringify(matchWithPlayers))
		navigate('/' + tournamentUid + '/match')
	}

	// Формируем данные для таблицы
	function setString(match, inverted, playerOne, playerTwo) {
		// Получаем все сеты у всех матчей
		const sets = match[0].sets
		const cursor = 'pointer'

		// Проверяем, если сетов нет
		if (sets.length === 0) {
			return (
				<div className="roundrobin-grid__cell-score" style={{ cursor: auth ? cursor : '' }}>
					<p onClick={() => openMatch(match, playerOne, playerTwo, inverted)}>
						<span>
							0:0
						</span>
					</p>
				</div>
			)
		} else {
			// Если сеты есть
			return (
				<div
					className="roundrobin-grid__cell-score"
					style={{ cursor: auth ? cursor : '' }}
				>
					{
						sets.map((set, setIndex) => {
							const tieBreakScore1 = set.tie_break_score1 ? set.tie_break_score1.toString() : ''
							const tieBreakScore2 = set.tie_break_score2 ? set.tie_break_score2.toString() : ''

							if (inverted) {
								return (
									<p
										key={setIndex}
										onClick={() => openMatch(match, playerOne, playerTwo, inverted)}
									>
										<span>
											{ set.score2 }
											<sup>
												{ tieBreakScore2 }
											</sup>
											:
											{ set.score1 }
											<sup>
												{ tieBreakScore1 }
											</sup>
										</span>
									</p>
								)
							} else {
								return (
									<p
										key={setIndex}
										onClick={() => openMatch(match, playerOne, playerTwo, inverted)}
									>
										<span>
											{ set.score1 }
											<sup>
												{ tieBreakScore1 }
											</sup>
											:
											{ set.score2 }
											<sup>
												{ tieBreakScore2 }
											</sup>
										</span>
									</p>
								)
							}
						})
					}
				</div>
			)
		}
	}

	function checkMatch(playerOne, playerTwo) {
		const match = group?.matches.find(
			match => (match.side1_uid === playerOne.nodeUid && match.side2_uid === playerTwo.nodeUid) ||
				(match.side2_uid === playerOne.nodeUid && match.side1_uid === playerTwo.nodeUid)
		)

		return !match?.is_finished && match?.sets.length > 0
	}

	const showColumn = useMemo(() => {
		return getColumnsWithSameResults(group, round_robin_ranking_system)
	}, [group, round_robin_ranking_system])

	function onDragEnd(result) {
		if (!result.destination) {
			return
		}

		const { destination, source } = result

		const updatedParticipants = Array.from(popupList)
		const [removed] = updatedParticipants.splice(source.index, 1)
		updatedParticipants.splice(destination.index, 0, removed)
		setPopupList(updatedParticipants)
	}

	function updateResultManually() {
		setOpenResultEditor(false)
	}

	function dragItemStyle(provided, snapshot) {
		const backgroundColor = 'var(--palette-live-grey-8)'
		const borderStyle = '1px solid var(--palette-live-grey-6)'
		const borderRadius = '11px'

		return {
			userSelect: 'none',
			backgroundColor: snapshot.isDragging ? backgroundColor : '',
			borderRadius: snapshot.isDragging ? borderRadius : '',
			border: snapshot.isDragging ? borderStyle : '',
			...provided.draggableProps.style
		}
	}

	return (
		<>
			<div className="roundrobin-grid__wrapper">
				<div className="roundrobin-grid__sheet">
					<div className="roundrobin-grid__content--sticky">
						<div className="roundrobin-grid__row row-header">
							<div className="roundrobin-grid__header group-name__container">
								<div className="group-name">
									{ group.name }
								</div>
							</div>
						</div>

						{
							grid?.map((itemVer, indexVertical) => {
								return (
									<div
										className="roundrobin-grid__row"
										key={indexVertical}
									>
										{
											itemVer.player1 ?
												// Парный
												<div className="roundrobin-grid__cell participant-name__container">
													<div className="participant-name__order">
														{ indexVertical + 1 }
													</div>

													{
														itemVer.player1?.formedName ?
															<div className="participant-name__item">
																<span>
																	{ itemVer.player1.formedName }
																</span>

																<span>
																	{ itemVer.player2.formedName }
																</span>
															</div>
															:
															<div className="participant-name__item">
																{ itemVer.name }
															</div>
													}
												</div>
												:
												// Одиночный
												<div className="roundrobin-grid__cell participant-name__container">
													<div className="participant-name__order">
														{ indexVertical + 1 }
													</div>

													<div className="participant-name__item">
														{
															itemVer?.formedName ?
																`${ itemVer?.formedName }`
																:
																itemVer?.name
														}
													</div>
												</div>
										}
									</div>
								)
							})
						}
					</div>

					<div className="roundrobin-grid__row empty"></div>

					<div className="roundrobin-grid__content">
						<div className="roundrobin-grid__row row-header">
							{
								grid?.map((_, index) => {
									return (
										<div
											className="roundrobin-grid__header"
											key={index}
										>
											<div className="participant-name__order">
												{ index + 1 }
											</div>
										</div>
									)
								})
							}
						</div>
						{
							grid?.map((itemVer, indexVertical) => {
								return (
									<div
										className="roundrobin-grid__row"
										key={indexVertical}
									>
										{
											grid?.map((itemHor, indexHorizontally) =>
												<div
													className={
														`roundrobin-grid__cell ${indexVertical === indexHorizontally ?
															'roundrobin-grid__cell--grey'
															:
															''
														}`
													}
													key={indexHorizontally}
												>
													{
														indexVertical === indexHorizontally ?
															''
															:
															<div className="roundrobin-grid__cell-sets">
																{
																	getMatch(itemVer, itemHor, indexVertical, indexHorizontally)
																}
															</div>
													}

													{
														checkMatch(itemVer, itemHor) &&
															<Ball className="roundrobin-grid__icon-ball" />
													}
												</div>
											)
										}
									</div>
								)
							})
						}
					</div>

					<div className="roundrobin-grid__row empty"></div>

					<div className="roundrobin-grid__content">
						<div className="roundrobin-grid__row row-header">
							<div className="roundrobin-grid__header">
								M
							</div>

							{
								showColumn.sets &&
									<div className="roundrobin-grid__header">
										S

										{
											round_robin_ranking_system === RR_RANKING_SYST_EQUAL &&
												<span className="roundrobin-grid__header--inner">
													{ t('between each other') }
												</span>
										}
									</div>
							}

							{
								showColumn.games &&
									<div className="roundrobin-grid__header">
										G

										{
											round_robin_ranking_system === RR_RANKING_SYST_EQUAL &&
												<span className="roundrobin-grid__header--inner">
													{ t('between each other') }
												</span>
										}
									</div>
							}

							<div
								onClick={() => setOpenResultEditor(true)}
								className="roundrobin-grid__header result-cell"
							>
								R
							</div>
						</div>

						{
							grid?.map((_, indexVertical) => {
								return (
									<div
										className="roundrobin-grid__row"
										key={indexVertical}
									>
										<div className="roundrobin-grid__cell result-cell__score">
											<span className="roundrobin-grid__cell-match-result">
												{ gridResults[indexVertical]?.match_points_won } – { gridResults[indexVertical]?.match_points_lost }
											</span>
										</div>

										{
											showColumn.sets &&
												<div className="roundrobin-grid__cell--double">
													{
														includeInDisput(gridResults[indexVertical], group, 1) ?
															<div className="roundrobin-grid__cell-container result-cell__score">
																<span className="roundrobin-grid__cell--inner">
																	{ gridResults[indexVertical]?.set_points_won } – { gridResults[indexVertical]?.set_points_lost }
																</span>

																<span className="roundrobin-grid__cell--inner">
																	{ convertInPercent(gridResults[indexVertical], 'set', group) }
																</span>
															</div>
															:
															<div className="roundrobin-grid__cell-container">
																<span className="roundrobin-grid__cell--inner"></span>

																<span className="roundrobin-grid__cell--inner"></span>
															</div>
													}
												</div>
										}

										{
											showColumn.games &&
												<div className="roundrobin-grid__cell--double">
													{
														includeInDisput(gridResults[indexVertical], group, 2) ?
															<div className="roundrobin-grid__cell-container result-cell__score">
																<div className="roundrobin-grid__cell--inner">
																	{ gridResults[indexVertical]?.game_points_won } – { gridResults[indexVertical]?.game_points_lost }
																</div>

																<div className="roundrobin-grid__cell--inner">
																	{ convertInPercent(gridResults[indexVertical], 'game', group) }
																</div>
															</div>
															:
															<div className="roundrobin-grid__cell-container">
																<div className="roundrobin-grid__cell--inner"></div>

																<div className="roundrobin-grid__cell--inner"></div>
															</div>
													}
												</div>
										}

										<div
											className="roundrobin-grid__cell result-cell result-cell__score"
											onClick={() => setOpenResultEditor(true)}
										>
											{ gridResults[indexVertical]?.ranking }
										</div>
									</div>
								)
							})
						}
					</div>
				</div>
			</div>

			{
				openResultEditor && auth &&
					<Popup
						title={t('Distribution of final places in the group')}
						leftTitle={true}
					>
						<div className="roundrobin-grid__popup-container">
							<DragDropContext onDragEnd={onDragEnd}>
								<Droppable droppableId="roundrobin-grid__popup-list">
									{
										(provided) => (
											<div
												{...provided.droppableProps}
												ref={provided.innerRef}
												className="roundrobin-grid__popup-list"
											>
												<div className="roundrobin-grid__popup-list-container">
													{
														popupList.map((item, index) => (
															<Draggable
																key={item.uid}
																draggableId={item.uid}
																index={index}
															>
																{
																	(provided, snapshot) => (
																		<div
																			ref={provided.innerRef}
																			{...provided.draggableProps}
																			{...provided.dragHandleProps}
																			className="roundrobin-grid__popup-item-container"
																			style={dragItemStyle(provided, snapshot)}
																		>
																			{
																				type === TYPE_SINGLES ?
																					<div className="roundrobin-grid__popup-item">
																						<span className="roundrobin-grid__popup-item-number">
																							{ index + 1 }
																						</span>

																						<span className="roundrobin-grid__popup-item-name">
																							{ item.formedName }
																						</span>
																					</div>
																					:
																					<div className="roundrobin-grid__popup-item">
																						<span className="roundrobin-grid__popup-item-number">
																							{ index + 1 }
																						</span>

																						<div className="roundrobin-grid__popup-item-double">
																							<span className="roundrobin-grid__popup-item-name">
																								{ item.player1.formedName }
																							</span>

																							<span className="roundrobin-grid__popup-item-name">
																								{ item.player2.formedName }
																							</span>
																						</div>
																					</div>
																			}

																			<DND />
																		</div>
																	)
																}
															</Draggable>
														))
													}
												</div>

												{ provided.placeholder }
											</div>
										)
									}
								</Droppable>
							</DragDropContext>
						</div>

						<div className="roundrobin-grid__popup-button-container">
							<RoundedButton
								title={t('Cancel')}
								onClick={() => setOpenResultEditor(false)}
								background={'white'}
								border
							/>

							<RoundedButton
								title={t('Save')}
								onClick={updateResultManually}
								border
							/>
						</div>
					</Popup>
			}
		</>
	)
}

export default RoundRobinGrid