import { memo, useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import dayjs from 'dayjs'
import customParseFormat from 'dayjs/plugin/customParseFormat'
import { Field, Formik } from 'formik'
import { useSelector } from 'react-redux'

import './TournamentForm.scss'
import { useGetCommunityQuery, useAddTournamentMutation } from '../../redux/communityService/communityApiSlice'
import { useUpdateTournamentMutation } from '../../redux/singleTournamentService/singleTournamentApiSlice'
import {
	TYPE_SINGLES,
	TYPE_DOUBLES,
	RR_RANKING_SYST_EQUAL,
	RR_RANKING_SYST_GROUP,
	T2,
	T1
} from '../../utils/constants'
import MySelect from '../../reusableComponents/Select/Select'
import Input from '../../reusableComponents/Input/Input'
import Button from '../../reusableComponents/Button/Button'
import ReactTextarea from '../../reusableComponents/Textarea/Textarea'
import { selectTournament } from '../../redux/selectors'
import RadioButton from '../../reusableComponents/RadioButton/RadioButton'
import RoundedBlock from '../../reusableComponents/RoundedBlock/RoundedBlock'
import Header from '../../reusableComponents/Header/Header'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu'
import Loader from '../../reusableComponents/Loader/Loader'

function TournamentForm() {
	dayjs.extend(customParseFormat)
	const { t } = useTranslation()
	const [addTournament] = useAddTournamentMutation()
	const [updateTournament] = useUpdateTournamentMutation()
	const communityUid = localStorage.getItem('communityUid')
	const { data = {}, error, isLoading } = useGetCommunityQuery({ community_uid: communityUid }, { skip: !communityUid })
	const navigate = useNavigate()
	const {
		uid,
		venue_uid,
		start_date,
		type,
		description,
		round_robin_ranking_system,
		name
	} = useSelector(selectTournament)

	const venues = useMemo(() => {
		const arrayOfObjects = data?.venues?.map((el) => {
			return {
				label: el.name,
				value: el.uid
			}
		})

		arrayOfObjects?.unshift({
			label: t('Not selected'),
			value: ''
		})

		return arrayOfObjects
	}, [data?.venues, t])

	const tournamentVenue = useMemo(() => {
		const foundVenue = data?.venues?.find((item) => {
			return item.uid === venue_uid
		})

		if (foundVenue) {
			return {
				label: foundVenue.name,
				value: foundVenue.uid,
				address_string: foundVenue.address_string
			}
		} else {
			return null
		}
	}, [data?.venues, venue_uid])

	function validateName(name) {
		const regex = /^(\s*)$/

		if (regex.test(name)) {
			return t('Enter name')
		}
	}

	function validateDate(date) {
		if (!date) {
			return t('Enter start date')
		}
	}

	function getBody(values) {
		const { name, venue, date, type, roundRobinRankingSystem, description, tier } = values

		return {
			'language_code': data?.language_code,
			'name': name,
			'type': type,
			'venue_uid': venue.value,
			'start_date': date,
			'description': description,
			'community_uid': data?.uid,
			'tier': tier,
			'round_robin_ranking_system': roundRobinRankingSystem
		}
	}

	async function handleSubmitCreate(values) {
		if (!communityUid) return

		const body = getBody(values)

		try {
			const response = await addTournament(body).unwrap()

			navigate(`/${response?.uid}/draws/1`)
		} catch (e) {
			console.log('addTournaments catched error', e)
		}
	}

	// Редактируем уже созданный турнир
	async function handleSubmitEdit(values) {
		if (!communityUid) return

		const body = getBody(values)

		try {
			const response = await updateTournament({
				community_uid: communityUid,
				tournament_uid: uid,
				body
			}).unwrap()

			navigate(-1)
		}
		catch (e) {
			console.log('updateTournament catched error', e)
		}
	}

	if (error) { console.log(error) }

	return (
		<>
			<Header
				title={!uid ? t('Tournament creation') : t('Edition')}
				back
				backUrl={-1}
				border
			/>

			<main className="tournament-form">
				{
					isLoading ?
						<Loader />
						:
						<Formik
							initialValues={{
								name: name || '',
								venue: tournamentVenue || '',
								date: start_date || '',
								type: type || TYPE_SINGLES,
								roundRobinRankingSystem: round_robin_ranking_system || RR_RANKING_SYST_GROUP,
								description: description || '',
								tier: T1
							}}
							onSubmit={async (values) => {
								if (uid) {
									await handleSubmitEdit(values)
								} else {
									await handleSubmitCreate(values)
								}
							}}
						>
							{({ values, errors, touched, handleSubmit, handleChange, setFieldError, setFieldValue }) => (
								<form
									onSubmit={handleSubmit}
									noValidate
									className="tournament-form__wrapper"
								>
									<Field
										id={'name'}
										name="name"
										onChange={handleChange}
										validate={validateName}
										onClick={() => setFieldError('name', '')}
										type="text"
										value={values.name}
										placeholder={t('Name') + ' *'}
										as={Input}
										touched={touched.name}
										error={errors.name}
									/>

									<Field
										id={'venue'}
										onChange={(option) => setFieldValue('venue', option)}
										options={venues}
										value={values.venue}
										placeholder={t('Venue')}
										as={MySelect}
									/>

									<Field
										id={'date'}
										name={'date'}
										value={values.date}
										onChange={handleChange}
										validate={validateDate}
										onClick={() => setFieldError('date', '')}
										type="date"
										placeholder={t('Start date') + ' *'}
										as={Input}
										touched={touched.date}
										error={errors.date}
									/>

									<RoundedBlock background>
										<Field
											id="radio-single"
											name="type"
											value="singles"
											checked={values.type === TYPE_SINGLES}
											onChange={handleChange}
											label={t('Single tournament')}
											as={RadioButton}
											borderBottom
										/>

										<Field
											id="radio-double"
											name="type"
											value="doubles"
											checked={values.type === TYPE_DOUBLES}
											onChange={handleChange}
											label={t('Double tournament')}
											as={RadioButton}
										/>
									</RoundedBlock>

									<RoundedBlock background>
										<Field
											id="radio-t1"
											name="tier"
											value={T1}
											checked={values.tier === T1}
											onChange={handleChange}
											label={t('Режим редактирования Т1')}
											as={RadioButton}
											borderBottom
										/>

										<Field
											id="radio-t2"
											name="tier"
											value={T2}
											checked={values.tier === T2}
											onChange={handleChange}
											label={t('Режим редактирования Т2')}
											as={RadioButton}
										/>
									</RoundedBlock>

									<Field
										id="description"
										value={values.description}
										onChange={handleChange}
										height={'150px'}
										placeholder={t('Tournament description')}
										as={ReactTextarea}
									/>

									<p className="tournament-form__description">
										{ t('A method for determining places in a round-robin group when several participants have equal points') }
									</p>

									<RoundedBlock background>
										<Field
											id="radio-persentage--first"
											name="percentage"
											type="radio"
											value={RR_RANKING_SYST_GROUP}
											onChange={() => setFieldValue('roundRobinRankingSystem', RR_RANKING_SYST_GROUP)}
											checked={
												values.roundRobinRankingSystem === RR_RANKING_SYST_GROUP
											}
											label={t('By the percentage of all sets/games won')}
											as={RadioButton}
											borderBottom
										/>

										<Field
											id="radio-persentage--second"
											name="percentage"
											type="radio"
											value={RR_RANKING_SYST_EQUAL}
											onChange={() => setFieldValue('roundRobinRankingSystem', RR_RANKING_SYST_EQUAL)}
											checked={
												values.roundRobinRankingSystem === RR_RANKING_SYST_EQUAL
											}
											label={t('By the percentage of sets/games won in games with each other')}
											as={RadioButton}
										/>
									</RoundedBlock>
										
									<div className="tournament-form__button">
										<Button
											type={'submit'}
											title={t(!uid ? 'Create' : 'Save')}
										/>
									</div>
								</form>
							)}
						</Formik>
				}
			</main>

			<BottomMenu />
		</>
	)
}

export default memo(TournamentForm)