class LocalStorageManager {
	constructor() {
		this.setStore()
	}

	setStore() {
		this.storage = {
			communityName: localStorage.getItem('communityName'),
			communityUid: localStorage.getItem('communityUid'),
			access: localStorage.getItem('access'),
			refresh: localStorage.getItem('refresh'),
			guestCode: localStorage.getItem('guestCode'),
			authorized: localStorage.getItem('authorized'),
			communities: this.getCommunitiesObject(),
			lastCommunityUid: localStorage.getItem('lastCommunityUid')
		}
	}

	getStore() {
		return this.storage
	}

	setItem(key, value) {
		this.storage[key] = value

		if(key === 'communities') {
			value = JSON.stringify(value)
		}
		localStorage.setItem(key, value)
	}

	removeItem(key) {
		delete this.storage[key]
		localStorage.removeItem(key)
	}

	clear() {
		this.storage = {}
		localStorage.clear()
	}

	getCommunitiesObject() {
		const communities = localStorage.getItem('communities')
		if(!communities) {
			return
		}

		try {
			const parsedList = JSON.parse(communities)
			return parsedList
		} catch (e) {
			console.log('Could not parse communities')
		}
	}
}

export default new LocalStorageManager()