import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

import { getLocalStorageItem, setItemInLocalStorage } from '../utils/functions'

// import { refreshQuery } from "./authService/refreshApiSlice"
import { setError } from './errorService/errorSlice'
import { resetAuth, setAccessToken, setAuthorized } from './authService/authSlice'
import { setCommunityUid } from './communityService/communitySlice'

export const baseQuery = fetchBaseQuery({
	baseUrl: process.env.REACT_APP_BASE_URL,
	prepareHeaders: (headers) => {
		const access = getLocalStorageItem('access')
		const guestCode = getLocalStorageItem('guestCode')

		if (guestCode) {
			headers.set('x-auth-code', guestCode)
		} else if (access) {
			headers.set('authorization', `Bearer ${access}`)
		}

		return headers
	}
})

const baseQueryWithReauth = async (args, api, extraOptions) => {
	const { dispatch, getState } = api
	const state = getState()
	const authorized = getLocalStorageItem('authorized')
	const access = getLocalStorageItem('access')
	const refresh = getLocalStorageItem('refresh')
	const { communityUid }  = state.community
	const lastActiveCommunity = communityUid

	if (authorized && (!access || !refresh)) {
		dispatch(setAuthorized(false))
		dispatch(resetAuth())
		dispatch(setCommunityUid(lastActiveCommunity))
		setItemInLocalStorage('storage_wipe_reason', 'no_token/s')
		return
	}

	try {
		let result = await baseQuery(args, api, extraOptions)

		if (result?.error && ((authorized && result?.error?.status !== 401) || result?.error?.originalStatus === 500)) {
			dispatch(setError(result?.error))
		}

		if (result?.error?.status === 401 && refresh) {
			const refreshQuery = fetchBaseQuery({
				baseUrl: process.env.REACT_APP_BASE_URL,
				method: 'POST',
				body: { refresh: refresh }
			})

			const refreshResult = await refreshQuery('/token/refresh/', api, extraOptions)

			if (refreshResult?.data) {
				dispatch(setAccessToken(refreshResult.data.access))
				result = await baseQuery(args, api, extraOptions)
			} else {
				dispatch(resetAuth())
				dispatch(setCommunityUid(lastActiveCommunity))
				setItemInLocalStorage('storage_wipe_reason', 'refresh_token_invalid')
			}
		}

		console.log(result)

		return result
	} catch (e) {
		console.log('basic api catch error', e)
	}
}

export const apiSlice = createApi({
	baseQuery: baseQueryWithReauth,
	endpoints: builder => ({})
})