import React, { memo } from 'react'
import { useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'

import './TournamentStanding.scss'
import { useGetStageQuery } from '../../redux/stageService/stageApiSlice'
import { selectTournament } from '../../redux/selectors'
import { getParameters, useProcessStageData } from '../../utils/functions'
import { GROUP_TYPE_RR } from '../../utils/constants'
import RoundRobinGrid from '../../extendedComponents/RoundRobinGrid/RoundRobinGrid'
import OlympicPlusGrid from '../../extendedComponents/OlympicPlusGrid/OlympicPlusGrid'
import NoContent from '../../reusableComponents/NoContent/NoContent'
import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu'
import Loader from '../../reusableComponents/Loader/Loader'
import HeaderTournament from '../../reusableComponents/HeaderTournament/HeaderTournament'

import { sortByRanking } from './externalFunctions'

function TournamentStanding({ auth, tournamentName, tournamentStartDate, categories }) {
	const { t } = useTranslation()
	const { tournamentParams, stageNumber } = useParams()
	const { tournamentUid } = getParameters(tournamentParams)
	const { stages_count } = useSelector(selectTournament)

	const { data: stageData = {}, status } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, {
		skip: !stages_count || stages_count < 1
			|| stageNumber < 1 || stageNumber > stages_count,
		refetchOnMountOrArgChange: stages_count > 0
	})

	const { stage, stageStatus, stageExists } = useProcessStageData(stageData, status)

	function getGroup(group) {
		const { nodes, results } = sortByRanking(group)

		return { ...group, nodes, results }
	}

	return (
		<>
			<HeaderTournament
				name={tournamentName}
				date={tournamentStartDate}
				categories={categories}
				copyLink={auth ? true : false}
			/>

			<main className="tournament-standing">
				{
					stageExists && stageStatus === 'filled' ?
						stage?.levels?.map((level, levelIndex) => {
							return (
								<React.Fragment key={levelIndex}>
									{
										stage?.levels.length > 1 &&
										<p className="tournament-standing__level">
											{t('Level') + ' ' + (levelIndex + 1)}
										</p>
									}

									{
										level?.groups?.map((grp, index) =>
											<React.Fragment key={index}>
												{
													grp?.type === GROUP_TYPE_RR ?
														<RoundRobinGrid
															group={getGroup(grp)}
															auth={auth}
														/>
														:
														<OlympicPlusGrid
															group={grp}
															auth={auth}
															stage={stage}
														/>
												}
											</React.Fragment>
										)
									}
								</React.Fragment>
							)
						})
						: stageStatus && (status === 'fulfilled' || stages_count < 1) ?
							<NoContent
								generate={stageStatus}
								auth={auth}
								table={'grids'}
							/>
							:
							<Loader />
				}
			</main>

			<CombineTournamentMenu />
		</>
	)
}

export default memo(TournamentStanding)