import { memo } from 'react'
import Select from 'react-select'
import { useTranslation } from 'react-i18next'

import './Select.scss'

function MySelect({ onClick, options, required, onChange, value, placeholder }) {
	const { t } = useTranslation()

	const customStyles = {
		container: base => ({
			...base,
			marginBottom: '16px'
		}),
		dropdownIndicator: base => ({
			...base,
			color: '#7E7D7D'
		}),
		indicatorSeparator: base => ({
			...base,
			display: 'none'
		}),
		valueContainer: base => ({
			...base,
			height: '72px'
		}),
		control: (base, { isFocused }) => ({
			...base,
			backgroundColor: isFocused ? 'var(--palette-live-bg-0-white)' : 'var(--palette-live-bg-3)',
			border: isFocused ? '1px solid var(--palette-live-grey-1)' : '1px solid var(--palette-live-grey-5)',
			boxShadow: 'none',
			borderRadius: '9px',
		}),
		singleValue: base => ({
			...base,
			fontSize: '17px',
		}),
		option: (base, { isSelected }) => ({
			...base,
			fontSize: '18px',
			padding: '15px 10px'
		}),
		placeholder: (provided, state) => ({
			...provided,
			color: 'var(--palette-live-grey-3)',
		})
	}

	function handleMenuOpen() {
		if (onClick) {
			onClick()
		}
	}

	function noOptionsMessage() {
		return t('No options')
	}

	return (
		<Select
			styles={customStyles}
			options={options}
			theme={(theme) => ({
				...theme,
				borderRadius: 6,
				colors: {
					...theme.colors,
					primary: '#4694D9'
				},
			})}
			value={value.value ? value : null}
			onChange={onChange}
			onMenuOpen={handleMenuOpen}
			isSearchable={false}
			required={required}
			noOptionsMessage={noOptionsMessage}
			placeholder={placeholder}
		/>
	)
}

export default memo(MySelect)