import { memo, useState } from 'react'
import './DoubleButton.scss'

import { ReactComponent as BackIcon } from '../../icons/back.svg'

function DoubleButton({
	titleSmall,
	titleBig,
	onClickSmall,
	onClickBig,
	typeSmall,
	typeBig,
	arrow,
	fixed
}) {
	const [isClicked, setIsClicked] = useState(false)

	const handleRightButtonClick = (e) => {
		onClickBig && onClickBig(e)

		setIsClicked(true)
		setTimeout(() => setIsClicked(false), 600)
	}

	return (
		<div className={`${fixed ? 'double-button--fixed' : 'double-button'}`}>
			<button
				className="double-button--left"
				onClick={onClickSmall}
				type={typeSmall}
			>
				{!arrow ?
					titleSmall
					:
					<BackIcon className="back-icon" />
				}
			</button>

			<button
				className={`double-button--right ${isClicked ? 'clicked' : ''}`}
				onClick={handleRightButtonClick}
				type={typeBig}
			>
				{titleBig}
			</button>
		</div>
	)
}

export default memo(DoubleButton)