import { useState, useEffect, useCallback, memo, useMemo } from 'react'
import { useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'
import { useDispatch, useSelector } from 'react-redux'

import './TournamentParticipants.scss'
import { selectCommunity, selectParticipants, selectTournament } from '../../redux/selectors'
import { findPlayers, checkEquality, getParameters } from '../../utils/functions'
import { TYPE_SINGLES, TYPE_DOUBLES, T1 } from '../../utils/constants'
import {
	useAddSinglePlayersMutation,
	useRemoveParticipantMutation,
	useAddDoublesMutation,
	useRemoveDoubleMutation,
	useSeedDoublesMutation,
	useSeedParticipantsMutation
} from '../../redux/tournamentParticipantsService/tournamentParticipantsApiSlice'
import { setParticipants } from '../../redux/tournamentParticipantsService/tournamentParticipantsSlice'
import { useGetPlayersQuery } from '../../redux/playersService/playersApiSlice'
import { ReactComponent as ButtonDeleteIcon } from '../../icons/old_icon/minus-delete.svg'
import Popup from '../../reusableComponents/Popup/Popup'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'
import DoubleSearchForPlayers from '../../extendedComponents/DoubleSearchForPlayers/DoublesearchForPlayers'
import SinglePlayersSearch from '../../extendedComponents/SinglePlayersSearch/SinglePlayersSearch'
import TournamentMenu from '../../reusableComponents/TournamentMenu/TournamentMenu'
import HeaderTournament from '../../reusableComponents/HeaderTournament/HeaderTournament'
import { useGetStageQuery } from '../../redux/stageService/stageApiSlice'
import Loader from '../../reusableComponents/Loader/Loader'

import { checkParticipantsOrder, formArraysForRequests } from './externalFunctions'

function TournamentParticipants({ auth, tournamentRefetch, categories, tournamentStartDate, tournamentName }) {
	const [addSinglePlayers] = useAddSinglePlayersMutation()
	const [removeParticipant] = useRemoveParticipantMutation()
	const [addDouble] = useAddDoublesMutation()
	const [removeDouble] = useRemoveDoubleMutation()
	const [seedSinglePlayers] = useSeedParticipantsMutation()
	const [seedDoubles] = useSeedDoublesMutation()
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { tournamentParams } = useParams()
	const { tournamentUid } = getParameters(tournamentParams)
	const { communityUid } = useSelector(selectCommunity)
	const {
		players,
		doubles,
		type,
		stages_count,
		participants_count,
		tier
	} = useSelector(selectTournament)
	const participants = useSelector(selectParticipants)
	const [searchPlayersList, setSearchPlayersList] = useState([])
	const [popupIsOpen, setPopupIsOpen] = useState(false)
	const [showSaveButton, setShowSaveButton] = useState(false)

	const { data: communityPlayers = [], status } = useGetPlayersQuery(
		{ community_uid: communityUid }, { skip: !auth }
	)
	const { data: stageData = {}, status: statusStage } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber: 1
	}, { skip: !stages_count || stages_count < 1, refetchOnMountOrArgChange: stages_count > 0 })

	useEffect(() => {
		if (players || doubles) {
			dispatch(setParticipants({ participants: players || doubles, type }))
		}
	}, [dispatch, players, doubles, type])

	useEffect(() => {
		if (communityPlayers.length > 0) {
			setSearchPlayersList(communityPlayers)
		}
	}, [communityPlayers])

	const enableToChangeList = useMemo(() => {
		if (Object.keys(stageData).length < 1) return true

		if (tier === T1) {
			const playedSetExists = stageData.levels.some(lvl => {
				return lvl.groups.some(grp => {
					return grp.matches.some(match => match.sets.length > 0)
				})
			})
			return !playedSetExists
		} else {
			return true
		}
	}, [stageData, tier])

	const showPopup = useCallback(() => {
		if (participants?.length > 7 || !enableToChangeList) {
			return
		}

		setPopupIsOpen(true)

		document.body.style.overflow = 'hidden'
	}, [participants, enableToChangeList])

	const closePopup = useCallback(() => {
		setPopupIsOpen(false)
		setShowSaveButton(!checkEquality(participants, players || doubles))

		document.body.style.overflow = 'auto'
	}, [participants, players, doubles])

	const search = useCallback((value) => {
		if (value !== '') {
			const foundPlayers = findPlayers(value, communityPlayers)

			setSearchPlayersList(foundPlayers)
		} else {
			setSearchPlayersList(communityPlayers)
		}
	}, [communityPlayers])

	function onDragEnd(result) {
		if (!auth || !result.destination) {
			return
		}

		const { destination, source } = result

		const updatedParticipants = Array.from(participants)
		const [removed] = updatedParticipants.splice(source.index, 1)
		updatedParticipants.splice(destination.index, 0, removed)
		dispatch(setParticipants({ participants: updatedParticipants, type }))
		setShowSaveButton(checkEquality(participants, players || doubles))
	}

	function removeParticipantFromList(participant) {
		const updatedList = participants?.filter(p => participant?.uid !== p?.uid)
		dispatch(setParticipants({ participants: updatedList, type }))
		setShowSaveButton(!checkEquality(updatedList, players || doubles))
	}

	async function handleSaveListButton() {
		setShowSaveButton(false)
		const { added, removed } = formArraysForRequests(participants, players || doubles, type)
		let state = [...players || doubles]
		let updates = [...participants]

		if (added?.length > 0) {
			const newParticipants = await includeParticipant(added)

			if (type === TYPE_DOUBLES) {
				updates = updates?.map(el => {
					const found = newParticipants?.find(p => el?.uid !== p?.uid &&
						el?.player1?.uid === p?.player1_uid && el?.player2?.uid === p?.player2_uid)

					if (found) {
						const updatedParticipant = { ...el, uid: found?.uid }
						state?.push(updatedParticipant)

						return updatedParticipant
					} else {
						return el
					}
				})
			}
		}

		if (removed?.length > 0) {
			await excludeParticipant(removed)

			state = state?.filter(el => {
				return removed.find(item => el?.uid !== item)
			})
		}

		const seed = checkParticipantsOrder(updates, state, type)

		if (seed?.length > 0) {
			await saveSeed(seed)
		}

		tournamentRefetch()
	}

	async function includeParticipant(newPlayers) {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'player_uids': newPlayers
				}

				response = await addSinglePlayers({ tournament_uid: tournamentUid, body: body }).unwrap()

			} else if (type === TYPE_DOUBLES) {
				body = {
					'doubles': newPlayers
				}

				response = await addDouble({ tournament_uid: tournamentUid, body: body }).unwrap()
			}

			if (response?.error) {
				console.log('Include participant return error', response?.error)

				return
			}

			return response
		} catch (e) {
			console.log('Include participant catch error', e)
		}
	}

	async function excludeParticipant(removedPlayers) {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'player_uids': removedPlayers
				}

				response = await removeParticipant({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			} else if (type === TYPE_DOUBLES) {
				body = {
					'double_uids': removedPlayers
				}

				response = await removeDouble({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			}

			if (response?.error) {
				console.log('Exclude participant return error', response?.error)

				return
			}

			return response
		} catch (e) {
			console.log('Exclude participant catch error', e)
		}
	}

	async function saveSeed(seed) {
		try {
			let body = null
			let response = {}

			if (type === TYPE_SINGLES) {
				body = {
					'player_uids': seed
				}

				response = await seedSinglePlayers({ tournament_uid: tournamentUid, body: body }).unwrap()

			} else if (type === TYPE_DOUBLES) {
				body = {
					'double_uids': seed
				}

				response = await seedDoubles({ tournament_uid: tournamentUid, body: body }).unwrap()
			}

			if (response?.error) {
				console.log('Seed participant return error', response?.error)

				return
			}

			return response?.doubles
		} catch (e) {
			console.log('Seed participant catch error', e)
		}
	}

	function dragItemStyle(provided, snapshot) {
		let style = {
			...provided.draggableProps.style
		}
		const borderStyle = '1px solid var(--palette-live-grey-6)'
		const borderRadius = '11px'
		const backgroundColor = 'var(--palette-live-grey-8)'

		if (snapshot.isDragging) {
			style = {
				...style,
				border: borderStyle,
				borderRadius,
				backgroundColor
			}
		}

		return style
	}

	function getWrapperStyle() {
		const gapFromMenu = '80px'

		return {
			paddingBottom: showSaveButton ? gapFromMenu : ''
		}
	}

	return (
		<>
			<HeaderTournament
				name={tournamentName}
				date={tournamentStartDate}
				categories={categories}
				copyLink={auth ? true : false}
			/>

			{(status !== 'fulfilled' && auth) || (statusStage !== 'fulfilled' && stages_count > 0) ?
				<Loader />
				:
				<main
					className="tournament-participants"
					style={getWrapperStyle()}
				>
					<div className="tournament-participants__wrapper">
						<p className="tournament-participants__title">
							<span className="tournament-participants__count">
								{participants_count}
							</span>

							{t(auth ? 'Saved' : 'Participate')}
						</p>

						{
							auth &&
							<div className="tournament-participants__button-add">
								<RoundedButton
									title={t('Add participant')}
									onClick={showPopup}
									disabled={participants?.length > 7 || !enableToChangeList}
									border={participants?.length < 8 || enableToChangeList}
								/>
							</div>
						}

						{
							!enableToChangeList && auth ?
								<p className="tournament-participants__alert">
									Появились первые результаты игр. Добавление новых участников ограничено
								</p>
								: participants?.length > 7 && auth ?
									<p className="tournament-participants__alert">
										Вы достигли максимального количества игроков
									</p>
									:
									''
						}

						<div
							className="tournament-participants__list-wrapper"
							style={{ border: participants.length < 1 ? 'none' : '' }}
						>
							<DragDropContext onDragEnd={onDragEnd}>
								<Droppable droppableId="participants--singles">
									{
										(provided) => (
											<div
												className="tournament-participants--singles"
												{...provided.droppableProps}
												ref={provided.innerRef}
											>
												{
													participants?.map((el, index) =>
														<Draggable
															key={el.uid}
															draggableId={el.uid}
															index={index}
															isDragDisabled={!enableToChangeList || !auth ? true : false}
														>
															{
																(provided, snapshot) => (
																	<div
																		className="tournament-participants__item"
																		ref={provided.innerRef}
																		{...provided.draggableProps}
																		{...provided.dragHandleProps}
																		style={dragItemStyle(provided, snapshot)}
																	>
																		{
																			type === TYPE_SINGLES ?
																				<>
																					<p className="tournament-participants__number">
																						{index + 1}
																					</p>

																					<p className="tournament-participants__name">
																						{`${el?.last_name} ${el.first_name}`}
																					</p>

																					{
																						auth && !enableToChangeList < 1 &&
																						<ButtonDeleteIcon
																							className="button-delete__icon"
																							onClick={() => removeParticipantFromList(el)}
																						/>
																					}
																				</>
																				:
																				<>
																					<p className="tournament-participants__number">
																						{index + 1}
																					</p>

																					<div className="tournament-participants__names">
																						<p className="tournament-participants__palyer--first">
																							{`${el?.player1?.last_name} ${el.player1?.first_name}`}
																						</p>

																						<p className="tournament-participants__palyer--second">
																							{`${el?.player2?.last_name} ${el?.player2?.first_name}`}
																						</p>
																					</div>

																					{
																						auth && !enableToChangeList < 1 &&
																						<ButtonDeleteIcon
																							className="button-delete__icon"
																							onClick={() => removeParticipantFromList(el)}
																						/>
																					}
																				</>
																		}
																	</div>
																)
															}
														</Draggable>
													)
												}

												{provided.placeholder}
											</div>
										)
									}
								</Droppable>
							</DragDropContext>
						</div>
					</div>

					<div className="tournament-participants__button-save">
						{
							showSaveButton &&
							<RoundedButton
								onClick={handleSaveListButton}
								title={t('Save')}
								border
							/>
						}
					</div>

					{
						popupIsOpen && auth &&
						<Popup
							title={'Add participant'}
							onClose={() => closePopup(false)}
							close
						>
							{
								type === TYPE_SINGLES &&
								<SinglePlayersSearch
									search={search}
									searchPlayersList={searchPlayersList}
									participants={participants}
									type={type}
									playersLoading={status}
								/>
							}

							{
								type === TYPE_DOUBLES &&
								<DoubleSearchForPlayers
									search={search}
									searchPlayersList={searchPlayersList}
									participants={participants}
									type={type}
									playersLoading={status}
								/>
							}
						</Popup>
					}
				</main>
			}

			<TournamentMenu />
		</>
	)
}

export default memo(TournamentParticipants)