import { apiSlice } from '../apiSlice'

export const authApiSlice = apiSlice.injectEndpoints({
	reducerPath: 'loginApi',
	tagTypes: ['Login'],
	endpoints: builder => ({
		login: builder.mutation({
			query: (body) => ({
				url: '/token/',
				method: 'POST',
				body
			})
		}),
		invalidatesTags: ['Login']
	})
})

export const { useLoginMutation } = authApiSlice