import { memo, useState, useCallback, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector, useDispatch } from 'react-redux'
import SmoothCollapse from 'react-smooth-collapse'
import { useNavigate, useParams, useLocation } from 'react-router-dom'

import './StageGeneration.scss'
import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu'
import { ReactComponent as ArrowDownIcon } from '../../icons/arrow_down.svg'
import HeaderTournament from '../../reusableComponents/HeaderTournament/HeaderTournament'
import { ReactComponent as PlusWhiteIcon } from '../../icons/plus_white.svg'
import { ReactComponent as RemoveIcon } from '../../icons/remove.svg'
import ArrowLinkBlock from '../../reusableComponents/ArrowLinkBlock/ArrowLinkBlock'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'
import { selectTournament } from '../../redux/selectors'
import {
	useGetStageQuery,
	useGenerateStageMutation,
	useRemoveStageMutation
} from '../../redux/stageService/stageApiSlice'
import { setPlayersList, setChosenPlayerInList, setCurrentStageNumber } from '../../redux/stageService/stageSlice'
import { formNodes } from '../StageForm/externalFunctions'
import Popup from '../../reusableComponents/Popup/Popup'
import Button from '../../reusableComponents/Button/Button'
import Loader from '../../reusableComponents/Loader/Loader'

import { calculateNumberOfGroups, calculateNumberOfPlayers } from './externalFunctions'

function StageGeneration({
	tournamentName,
	tournamentStartDate,
	categories,
	tournamentUid,
	tournamentRefetch
}) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const location = useLocation()
	const { type, doubles, players, stages_count } = useSelector(selectTournament)
	const dispatch = useDispatch()
	const [showMainMenu, setShowMainMenu] = useState(false)
	const [groupType, setGroupType] = useState(1)
	const [count, setCount] = useState()
	const [patch, setPatch] = useState(location.pathname.split('/'))
	const [generateStage] = useGenerateStageMutation()
	const { stageNumber } = useParams()
	// Функция удаления этапа
	const [removeStage] = useRemoveStageMutation()
	const [removeStageConfirmation, setRemoveStageConfirmation] = useState(false)

	const { data: stage = {}, status } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, { refetchOnMountOrArgChange: true }) // Stage data

	useEffect(() => {
		if (Object.keys(stage).length !== 0) {
			const { rating_initial } = stage
			const formedNodes = formNodes(rating_initial, players || doubles, type)

			dispatch(setPlayersList(formedNodes))
		}
	}, [stage, stageNumber, type, doubles, players, dispatch])

	useEffect(() => {
		setCount(calculateNumberOfGroups(players?.length || doubles?.length))
	}, [players, doubles])

	const handleGroupType = useCallback((number) => {
		if (number === 1) {
			setGroupType(1)
			const groupsNumber = calculateNumberOfGroups(players?.length || doubles?.length)
			setCount(groupsNumber)
		} else if (number === 2) {
			setGroupType(2)
			const firstGroupPlayers = calculateNumberOfPlayers(players?.length || doubles?.length)
			setCount(firstGroupPlayers)
		}
	}, [players, doubles])

	const handleMinus = useCallback(() => {
		if (count === 0) {
			return
		} else {
			setCount(prev => --prev)
		}
	}, [count])

	const handlePlus = useCallback(() => {
		if ((doubles && count > doubles.length - 1) || (players && count > players.length - 1)) {
			setCount(count)
		} else {
			setCount(prev => ++prev)
		}
	}, [count, doubles, players])

	const handleCounter = useCallback(() => {
		if ((doubles && count > doubles.length - 1) || (players && count > players.length - 1)) {
			return count
		}

		return count
	}, [count, doubles, players])

	const generateStageSubmit = (async (event) => {
		let body = null

		if (groupType === 1) {
			body = {
				'generate_method': 'equal',
				'groups_count': count
			}
		} else if (groupType === 2) {
			body = {
				'generate_method': 'unequal',
				'group1_participants_count': count
			}
		}

		try {
			const response = await generateStage({ tournament_uid: tournamentUid, stageNumber, body }).unwrap()

			navigate(`/${tournamentUid}/structure/${response?.order_number}`)
		}
		catch (e) {
			console.log('authomatical creation in stageCreation catch error', e)
		}
	})

	// Функция удаления этапа
	const handleRemoveStage = useCallback(async () => {
		try {
			let response = {}

			for (let i = stageNumber - 1; i < stages_count; i++) {
				response = await removeStage({
					tournament_uid: tournamentUid
				}).unwrap()

				if (response?.error) {
					console.log(`Remove stage return error: ${response?.error}`)
				}
			}

			tournamentRefetch()

			dispatch(setPlayersList([]))
			dispatch(setChosenPlayerInList({}))
			setRemoveStageConfirmation(false)

			if (response.stages_count > 0) {
				navigate(`/${tournamentUid}/structure/${response.stages_count}`)
				dispatch(setCurrentStageNumber(response.stages_count))
			} else {
				dispatch(setCurrentStageNumber(0))
				navigate(`/${tournamentUid}/structure/0`)
			}
		} catch (error) {
			console.log(`HandleRemoveStage stage catch error ${error}`)
		}
	}, [tournamentUid, dispatch, stageNumber, stages_count, removeStage, tournamentRefetch, navigate])

	if ((status === 'pending') || (status === 'uninitialized')) {
		return <Loader />
	}

	return (
		<div className={`stage-generation ${stages_count > 0 ? 'stage-generation--background' : ''}`}>
			<HeaderTournament
				name={tournamentName}
				date={tournamentStartDate}
				categories={categories}
			/>

			<main className="stage-generation__main">
				<div className="stage-generation__wrapper">
					<div className="stage-generation__container">
						<div className="stage-generation__header">
							<p className="stage-generation__quantity">
								{ players?.length || doubles?.length || 0 }
							</p>

							<p className="stage-generation__stage-number">
								Этап { patch[4] }
							</p>

							<ArrowDownIcon
								className={`arrow-down__icon ${showMainMenu ? 'arrow--up' : 'arrow--down'}`}
								onClick={() => setShowMainMenu(prev => !prev)}
							/>
						</div>

						<div className="stage-generation__list-participants">
							<SmoothCollapse expanded={showMainMenu}>
								{
									players?.map((value, index) => (
										<ArrowLinkBlock
											borderBottom
											key={index}
										>
											<div className="stage-generation__list-participants-container">
												<p className="stage-generation__list-participants-index">
													{ index + 1 }
												</p>

												<p className="stage-generation__list-participants-name">
													{ value?.first_name } { value?.formedName }
												</p>
											</div>
										</ArrowLinkBlock>
									))
								}

								{
									doubles?.map((value, index) => (
										<ArrowLinkBlock
											borderBottom
											key={index}
										>
											<div className="stage-generation__list-participants-container">
												<p className="stage-generation__list-participants-index">
													{ index + 1 }
												</p>

												<div>
													<p className="stage-generation__list-participants-name">
														{ value?.player1.first_name } { value?.player1.formedName }
													</p>

													<p className="stage-generation__list-participants-name">
														{ value?.player2.first_name } { value?.player2.formedName }
													</p>
												</div>
											</div>
										</ArrowLinkBlock>
									))
								}
							</SmoothCollapse>
						</div>

						<div className="stage-generation__radio-wrapper">
							<div className="stage-generation__radio-element">
								<input
									id="radio-1"
									type="radio"
									name="stageType"
									value="1"
									checked={groupType === 1 ? true : false}
 									onChange={() => handleGroupType(1)}
								/>

								<label
									htmlFor="radio-1"
									className="stage-generation__radio-lable"
								>
									{ t('Групповой') }
								</label>
							</div>

							<div className="stage-generation__radio-element">
								<input
									id="radio-2"
									type="radio"
									name="stageType"
									value="2"
									checked={groupType === 2 ? true : false}
 									onChange={() => handleGroupType(2)}
								/>

								<label
									htmlFor="radio-2"
									className="stage-generation__radio-lable"
								>
									{ t('Основной') }
								</label>
							</div>
						</div>

						<div className="stage-generation__counter-groupe">
							<p className="counter-groupe__name">
								{
									groupType === 1 ?
										'Количество групп'
										:
										'Кол-во участников в 1й группе'
								}
							</p>

							<div className="counter-groupe__container">
								<button className="counter-groupe__counter-button" onClick={() => handleMinus()}>
									<RemoveIcon className="remove-icon" />
								</button>

								<p className="counter-groupe__counter">
									{ handleCounter(count) }
								</p>

								<button className="counter-groupe__counter-button" onClick={() => handlePlus()}>
									<PlusWhiteIcon className="plus-white__icon" />
								</button>
							</div>
						</div>
					</div>

					<div className="stage-generation__button-wrapper">
						<RoundedButton
							title="Сгенерировать этап"
							onClick={(e) => generateStageSubmit(e)}
							border
							disabled={players?.length > 0 || doubles?.length > 0 ? false : true}
						/>
					</div>

					<button
						className="stage-generation__delete-button"
						onClick={() => setRemoveStageConfirmation(true)}
					>
						{t('Remove stage')}
					</button>
				</div>

				{
					removeStageConfirmation &&
						<Popup title="Это нельзя будет отменить">
							<p className="stage-generation__confirmation">
								{ t('Are you sure you want to remove stage?') }
							</p>

							<div className="stage-generation__confirmation-buttons">
								<Button
									title={t('Cancel')}
									onClick={() => setRemoveStageConfirmation(false)}
									marginRight={true}
									marginBottom={false}
									background="white"
								/>

								<Button
									title={t('Remove')}
									onClick={handleRemoveStage}
								/>
							</div>
						</Popup>
				}
			</main>

			<CombineTournamentMenu structure />
		</div>
	)
}

export default memo(StageGeneration)