import React, { useState, useEffect, memo, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector, useDispatch } from 'react-redux'
import { useNavigate, useParams } from 'react-router'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'

import './TableMatches.scss'
import { selectTournament } from '../../redux/selectors'
import { ALPHABET, NODE_TYPE_IN, NODE_TYPE_MIDDLE } from '../../utils/constants'
import { ReactComponent as Ball } from '../../icons/tennis_ball.svg'
import { getParameters } from '../../utils/functions'
import { setMatchData } from '../../redux/matchService/matchSlice'


function TableMatches({ auth, data }) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const { players, doubles } = useSelector(selectTournament)
	const { tournamentParams } = useParams()
	const { tournamentUid } = getParameters(tournamentParams)
	const [tableMatches, setTableMatches] = useState([])
	const [dropTableIndex, setDropTableIndex] = useState('')

	function openMatch(match) {
		if (!auth || match?.noMatch) {
			return
		}

		const updatedMatch = { ...match, uid: match.matchUid }
		dispatch(setMatchData(updatedMatch))
		sessionStorage.setItem('match', JSON.stringify(updatedMatch))
		navigate('/' + tournamentUid + '/match')
	}

	// updatedMatch для таблицы Матчи
	useEffect(() => {
		if (Object.keys(data).length !== 0) {
			const tableMatchesArray = data?.levels?.map((level, levelIndex) => {
				return level?.groups?.map((group, groupIndex) => {
					const matchesArray = group?.matches?.map(match => {
						if (doubles === null) {
							const nodeName = group.nodes.find((node) => {
								return node.double_uid === null &&
									node.player_uid === null &&
									node.type === NODE_TYPE_IN &&
									node.type === NODE_TYPE_MIDDLE
							})

							if (nodeName) {
								// Одиночный (если пока нет игроков)
								const nodeMatchSide1 = group.nodes.find((node) => {
									return node.uid === match.side1_uid
								})

								const nodeMatchSide2 = group.nodes.find((node) => {
									return node.uid === match.side2_uid
								})

								return {
									side1: {
										namePlayer: nodeMatchSide1 ? nodeMatchSide1?.name : '---'
									},
									side2: {
										namePlayer: nodeMatchSide2 ? nodeMatchSide2?.name : '---'
									},
									isFinished: match.is_finished,
									sets: [],
									doubleBoll: false,
									matchUid: match?.uid,
									name: group?.name,
									levelIndex: levelIndex,
									groupIndex: groupIndex,
									noMatch: !nodeMatchSide1 || !nodeMatchSide2 ? true : false
								}

							} else {
								// Одиночный (если есть игроки)
								const playerSide1 = group.nodes.find((item) => {
									return match.side1_uid === item.uid
								})
								const matchSide1 = players.find((item) => {
									return playerSide1.player_uid === item.uid
								})

								const playerSide2 = group.nodes.find((item) => {
									return match.side2_uid === item.uid
								})
								const matchSide2 = players.find((item) => {
									return playerSide2.player_uid === item.uid
								})

								return {
									side1: {
										namePlayer: matchSide1 ? matchSide1?.formedName : '---'
									},
									side2: {
										namePlayer: matchSide2 ? matchSide2?.formedName : '---'
									},
									isFinished: match.is_finished,
									name: group?.name,
									sets: match?.sets,
									doubleBoll: false,
									matchUid: match?.uid,
									levelIndex: levelIndex,
									groupIndex: groupIndex,
									noMatch: !matchSide1 || !matchSide2 ? true : false
								}
							}
						} else {
							const nodeName = group.nodes.find((node) => {
								return node.double_uid === null &&
									node.player_uid === null &&
									node.type === NODE_TYPE_IN &&
									node.type === NODE_TYPE_MIDDLE
							})

							if (nodeName) {
								// Парный (если пока нет игроков)
								const nodeMatchSide1 = group.nodes.find((node) => {
									return node.uid === match.side1_uid
								})

								const nodeMatchSide2 = group.nodes.find((node) => {
									return node.uid === match.side2_uid
								})

								return {
									side1: {
										namePlayer1: nodeMatchSide1 ? nodeMatchSide1?.name : '---'
									},
									side2: {
										namePlayer2: nodeMatchSide1 ? nodeMatchSide2?.name : '---'
									},
									isFinished: match.is_finished,
									sets: [],
									doubleBoll: true,
									matchUid: match?.uid,
									name: group?.name,
									levelIndex: levelIndex,
									groupIndex: groupIndex,
									noMatch: nodeMatchSide1 || nodeMatchSide2 ? true : false
								}
							}
							else {
								// Парный (если есть игроки)
								const playerSide1 = group.nodes.find((item, index, array) => {
									return match.side1_uid === item.uid
								})
								const matchSide1 = doubles.find((item, index, array) => {
									return playerSide1.double_uid === item.uid
								})

								const playerSide2 = group.nodes.find((item, index, array) => {
									return match.side2_uid === item.uid
								})
								const matchSide2 = doubles.find((item, index, array) => {
									return playerSide2.double_uid === item.uid
								})

								return {
									side1: {
										namePlayer1: matchSide1 ? matchSide1.player1.formedName : '---',
										namePlayer2: matchSide1 ? matchSide1.player2.formedName : '---'
									},
									side2: {
										namePlayer1: matchSide2 ? matchSide2.player1.formedName : '---',
										namePlayer2: matchSide2 ? matchSide2.player2.formedName : '---'
									},
									isFinished: match.is_finished,
									sets: match?.sets,
									doubleBoll: true,
									matchUid: match?.uid,
									name: group?.name,
									levelIndex: levelIndex,
									groupIndex: groupIndex,
									noMatch: playerSide2?.double_uid && playerSide1?.double_uid ? false : true
								}
							}
						}
					})

					return { matches: matchesArray }
				})
			}).flat(2)

			setTableMatches(tableMatchesArray)
		} else {
			return
		}
	}, [data, doubles, players])

	function getGroupName(index) {
		const groupName = ALPHABET[index]
		const groupTitle = t('Group') + ' ' + groupName

		return groupTitle
	}

	function dragRowStyle(provided, snapshot) {
		let style = {
			...provided.draggableProps.style
		}
		const borderStyle = '1px solid var(--palette-live-grey-6)'
		const borderRadius = '11px'
		const backgroundColor = 'var(--palette-live-grey-8)'

		if (snapshot.isDragging) {
			style = {
				...style,
				border: borderStyle,
				borderRadius,
				backgroundColor
			}
		}

		return style
	}

	const getDragItemStyle = useCallback((snapshot, tableIndex) => {
		const borderStyle = '1px solid var(--palette-live-grey-10)'
		const noBorderStyle = 'none'

		return {
			borderBottom: snapshot.isDragging ?
				noBorderStyle : tableIndex === dropTableIndex ? borderStyle : '',
			borderTop:
				tableIndex === dropTableIndex
					&& !snapshot.isDragging
					? borderStyle
					: ''
		}
	}, [dropTableIndex])

	const getStyleSetsBlock = useCallback((snapshot, tableIndex) => {
		return {
			...getDragItemStyle(snapshot, tableIndex)
		}
	}, [getDragItemStyle])

	function handleBeforeDragStart(result) {
		if (result.source) {
			const tableIndex = Number(result.source.droppableId.split('-')[1])

			setDropTableIndex(tableIndex)
		}
	}

	function handleDragEnd(result) {
		if (!result.destination) {
			return
		}

		const { destination, source } = result

		const tableIndex = Number(destination.droppableId.split('-')[1])
		const endIndex = destination.index
		const startIndex = source.index

		const table = tableMatches[tableIndex].matches
		const [removed] = table.splice(startIndex, 1)
		table.splice(endIndex, 0, removed)

		const updatedTableMatches = [...tableMatches]
		updatedTableMatches[tableIndex] = { matches: table }
		setTableMatches(updatedTableMatches)
		setDropTableIndex('')
	}

	return (
		<>
			{
				tableMatches?.map((tableMatch, tableMatchIndex) => {
					// Тут if?
					if (tableMatch?.matches.length > 0) {
						return <React.Fragment key={tableMatchIndex}>
							<p className="match-group__title">
								{ tableMatch?.matches[0]?.name }
							</p>

							<DragDropContext
								onBeforeDragStart={handleBeforeDragStart}
								onDragEnd={handleDragEnd}
							>
								<Droppable droppableId={`table-${tableMatchIndex}`}>
									{
										(provided) => (
											<div
												{...provided.droppableProps}
												ref={provided.innerRef}
												className="match-table__container"
											>
												{
													tableMatch.matches.map((match, matchIndex) => {
														return <Draggable
															key={match.matchUid}
															draggableId={match.matchUid}
															index={matchIndex}
															isDragDisabled={!auth}
														>
															{
																(provided, snapshot) => (
																	<div
																		ref={provided.innerRef}
																		{...provided.draggableProps}
																		{...provided.dragHandleProps}
																		className="match-table__row" key={matchIndex}
																		style={dragRowStyle(provided, snapshot)}
																	>
																		<div
																			className="match-table__cell cell-order"
																			style={
																				getDragItemStyle(snapshot, tableMatchIndex)
																			}
																		>
																			<p className="match-table__cell-order">
																				{matchIndex + 1}
																			</p>

																			<div className="match-table__cell-ball">
																				{
																					!match.isFinished && match.sets.length > 0 &&
																						<Ball className="match-table__cell-ball" />
																				}
																			</div>
																		</div>

																		<div
																			className="match-table__cell"
																			style={
																				getDragItemStyle(snapshot, tableMatchIndex)
																			}
																		>
																			{
																				match.doubleBoll === false ?
																					// Одиночный
																					<div
																						className="match-table__cell-names"
																					>
																						<p>{`${match.side1.namePlayer}`}</p>
																						<p>{`${match.side2.namePlayer}`}</p>
																					</div>
																					:
																					// Парный
																					!match.side2.namePlayer1 && !match?.side1?.namePlayer2 ?
																						<div className="match-table__cell-names">
																							<p>{`${match.side1.namePlayer1}`}</p>
																							<p>{`${match?.side2?.namePlayer2}`}</p>
																						</div>
																						:
																						<div className="match-table__cell-names">
																							<p>
																								{`${match.side1.namePlayer1}`}
																								<br />
																								{`${match?.side1?.namePlayer2}`}
																							</p>
																							<p>
																								{`${match.side2.namePlayer1}`}
																								<br />
																								{`${match?.side2?.namePlayer2}`}
																							</p>
																						</div>
																			}
																		</div>

																		<div
																			className="match-table__cell"
																			style={
																				getStyleSetsBlock(snapshot, tableMatchIndex)
																			}
																		>
																			<div className="match-table__cell-container">
																				<div className="match-table__cell-sets">
																					<div className="match-table__cell-sets-score">
																						{
																							match?.sets?.length !== 0 ? (
																								match?.sets?.map((tableMatchSets, tableMatchSetsIndex) => {
																									return (
																										<p
																											key={tableMatchSetsIndex}
																											onClick={() => openMatch(match)}
																										>
																											<span>
																												{tableMatchSets.score1} {
																													tableMatchSets.tie_break_score1 ?
																														<sup>
																															{tableMatchSets.tie_break_score1}
																														</sup>
																														:
																														''
																												}
																											</span>
																										</p>
																									)
																								})
																							)
																								:
																								(
																									match.side1.namePlayer !== '---' ?
																										<p onClick={() => openMatch(match)}>
																											<span>
																												0
																											</span>
																										</p>
																										:
																										<p>
																											<span>
																												-
																											</span>
																										</p>
																								)
																						}
																					</div>

																					<div className="match-table__cell-sets-score">
																						{
																							match?.sets?.length !== 0 ? (
																								match.sets?.map((tableMatchSets, tableMatchSetsIndex) => {
																									return (
																										<p
																											key={tableMatchSetsIndex}
																											onClick={() => openMatch(match)}
																										>
																											<span>
																												{tableMatchSets.score2} {
																													tableMatchSets.tie_break_score2 ?
																														<sup>
																															{tableMatchSets.tie_break_score2}
																														</sup>
																														:
																														''
																												}
																											</span>
																										</p>
																									)
																								})
																							)
																								:
																								(
																									match.side2.namePlayer !== '---' ?
																										<p onClick={() => openMatch(match)}>
																											<span>
																												0
																											</span>
																										</p>
																										:
																										<p>
																											<span>
																												-
																											</span>
																										</p>
																								)
																						}
																					</div>
																				</div>
																			</div>
																		</div>
																	</div>
																)
															}
														</Draggable>
													})
												}
												{provided.placeholder}
											</div>
										)
									}
								</Droppable>
							</DragDropContext>
						</React.Fragment>
					} else {
						return (
							<div
								className="match-group__notable"
								key={tableMatchIndex}
							>
								<p className="match-group__notable-group-name">{getGroupName(tableMatchIndex)}</p>
								<p className="match-group__notable-group-text">Нет матчей в расписании</p>
							</div>
						)
					}
				})
			}
		</>
	)
}

export default memo(TableMatches)