import './Match.scss'
import { useState, memo, useMemo, useCallback, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'
import { useNavigate, useLocation, Navigate } from 'react-router'
import { useKeenSlider } from 'keen-slider/react'
import SmoothCollapse from 'react-smooth-collapse'

import { useSaveSetMutation, useUpdateMatchMutation } from '../../redux/matchService/matchApiSlice'
import { T1, T2, TYPE_DOUBLES, TYPE_SINGLES } from '../../utils/constants'
import { selectTournament } from '../../redux/selectors'

import { ReactComponent as RemoveSet } from '../../icons/cross_set.svg'
import { ReactComponent as Plus } from '../../icons/match_plus.svg'
import { ReactComponent as Minus } from '../../icons/match_minus.svg'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'
import DoubleButton from '../../reusableComponents/DoubleButton/DoubleButton'
import Popup from '../../reusableComponents/Popup/Popup'
import Input from '../../reusableComponents/Input/Input'
import Header from '../../reusableComponents/Header/Header'

const newSetT1 = {
	order_number: 1,
	score1: 0,
	score2: 0,
	tie_break_score1: null,
	tie_break_score2: null
}
const newSetT2 = {
	order_number: 1,
	score1: 6,
	score2: 6,
	tie_break_score1: null,
	tie_break_score2: null
}

function getWayBack(location) {
	if (location) {
		const wayBack = location.pathname.replace('match', '')
		return wayBack + 'draws/1'
	} else {
		return '/tournaments'
	}
}

function invertSets(sets, inverted) {
	if (inverted) {
		return sets.map(set => {
			return {
				...set,
				score1: set.score2,
				score2: set.score1,
				tie_break_score1: set.tie_break_score2,
				tie_break_score2: set.tie_break_score1
			}
		})
	} else {
		return sets
	}
}

const getMatch = (matchSession, tier) => {
	if (matchSession && Object.keys(matchSession).length !== 0) {
		try {
			const currentMatch = JSON.parse(matchSession)
			if (currentMatch.sets.length < 1) {
				const newSet = tier === T1 ? newSetT1 : newSetT2
				currentMatch.sets = [{ ...newSet }]
			}

			return currentMatch
		} catch (e) {
			console.log(e)
		}
	} else {
		return
	}
}

function getActiveSetIndex(matchSession, tier) {
	const matchState = getMatch(matchSession, tier)
	if (!matchState) return 0

	const { sets } = matchState
	const lastIndex = sets.length > 0 ? sets.length - 1 : 0

	return lastIndex
}

function getActiveSet(sets, activeSetIndex) {
	return { ...sets[activeSetIndex] }
}

function getNameForPlaceholder(participant, type) {
	if (type === TYPE_SINGLES) {
		return participant.namePlayer
	} else {
		return `${participant.namePlayer1} / ${participant.namePlayer2}`
	}
}

function getInitialSlide(match, side) {
	if (!match) return 0
	const { sets } = match
	const sliderMaxIndex = 10

	if (sets.length > 0) {
		const lastIndex = sets.length - 1
		const activeSet = getActiveSet(sets, lastIndex)
		const { score1, score2 } = activeSet

		if (side === 1) {
			return score1 > sliderMaxIndex ? sliderMaxIndex : score1
		} else {
			return score2 > sliderMaxIndex ? sliderMaxIndex : score2
		}
	} else {
		return 0
	}
}

function Match() {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const location = useLocation()
	const matchSession = sessionStorage.getItem('match')
	const [updateMatch] = useUpdateMatchMutation()
	const [saveSet] = useSaveSetMutation()
	const { type, tier, name } = useSelector(selectTournament)

	const [tiebreak, setTiebreak] = useState(false)
	const [matchState, setMatchState] = useState(getMatch(matchSession, tier))
	const [activeSetIndex, setActiveSetIndex] = useState(getActiveSetIndex(matchSession, tier))
	const [popupOpened, setPopupOpened] = useState(false)
	const [firstInput, setFirstInput] = useState(0)
	const [secondInput, setSecondInput] = useState(0)
	const [loaded1, setLoaded1] = useState(false)
	const [loaded2, setLoaded2] = useState(false)
	const [indexOfSlider1, setIndexOfSlider1] = useState(getInitialSlide(matchState, 1))
	const [indexOfSlider2, setIndexOfSlider2] = useState(getInitialSlide(matchState, 2))
	const [touchedSlider, setTouchedSlider] = useState({ left: false, right: false })
	const [sliderIsMoving, setSliderIsMoving] = useState(false)

	const slidesAmount = 11
	const slidesArray = useMemo(() => Array.from({ length: slidesAmount }, (_, index) => index), [])

	const [sliderRef1, instanceRef1] = useKeenSlider(
		{
			animationEnded(slider) {
				setIndexOfSlider1(slider.track.details.rel)
				setSliderIsMoving(false)
			},
			animationStarted() {
				setSliderIsMoving(true)
			},
			created() {
				setLoaded1(true)
			},
			dragStarted() {
				setTouchedSlider(prev => { return { ...prev, left: true } })
			},
			initial: getInitialSlide(matchState, 1),
			loop: true,
			vertical: true,
			slides: {
				origin: 'center',
				spacing: 0,
				perView: 3
			},
			centeredSlides: true,
			mode: 'free-snap',
		},
	)
	const [sliderRef2, instanceRef2] = useKeenSlider(
		{
			animationEnded(slider) {
				setIndexOfSlider2(slider.track.details.rel)
				setSliderIsMoving(false)
			},
			animationStarted() {
				setSliderIsMoving(true)
			},
			created() {
				setLoaded2(true)
			},
			dragStarted() {
				setTouchedSlider(prev => { return { ...prev, right: true } })
			},
			initial: getInitialSlide(matchState, 2),
			loop: true,
			vertical: true,
			slides: {
				origin: 'center',
				spacing: 0,
				perView: 3
			},
			centeredSlides: true,
			mode: 'free-snap',
		}
	)

	useEffect(() => {
		if (!matchState || !touchedSlider.left) return

		updateScore(1)
	}, [indexOfSlider1])

	useEffect(() => {
		if (!matchState || !touchedSlider.right) return

		updateScore(2)
	}, [indexOfSlider2])

	function updateScore(side) {
		const leftSlider = 1
		const updatedMatch = { ...matchState }
		const sets = [...updatedMatch.sets]
		const activeSet = getActiveSet(sets, activeSetIndex)

		if (!tiebreak) {
			if (side === leftSlider) {
				activeSet.score1 = indexOfSlider1
			} else {
				activeSet.score2 = indexOfSlider2
			}
		} else {
			if (side === leftSlider) {
				activeSet.tie_break_score1 = indexOfSlider1
			} else {
				activeSet.tie_break_score2 = indexOfSlider2
			}
		}

		sets[activeSetIndex] = activeSet
		updatedMatch.sets = sets
		setMatchState(updatedMatch)

		const key = side === leftSlider ? 'left' : 'right'
		setTouchedSlider(prev => { return { ...prev, [key]: false } })
	}

	const changeSlidesInSwiper = useCallback((sets, index, tiebreak) => {
		if (sets.length > 0) {
			const lastSwiperIndex = slidesArray.length - 1
			const activeSet = getActiveSet(sets, index)
			const { score1, score2, tie_break_score1, tie_break_score2 } = activeSet

			let leftCurrentSlide = tiebreak ? tie_break_score1 : score1
			let rightCurrentSlide = tiebreak ? tie_break_score2 : score2

			leftCurrentSlide = getSlideNumber(leftCurrentSlide, lastSwiperIndex)
			rightCurrentSlide = getSlideNumber(rightCurrentSlide, lastSwiperIndex)

			if (!leftCurrentSlide && !rightCurrentSlide) {
				callSlidersMove(0, 0)
			} else {
				callSlidersMove(leftCurrentSlide, rightCurrentSlide)
			}
		} else {
			callSlidersMove(0, 0)
		}
	}, [slidesArray, instanceRef1, instanceRef2, callSlidersMove, getSlideNumber])

	function getSlideNumber(currentSlide, lastSwiperIndex) {
		if (currentSlide > lastSwiperIndex) {
			return 10
		} else if (!currentSlide && tiebreak && tier === T2) {
			return 7
		} else if (!currentSlide && tiebreak && tier === T1) {
			return 0
		} else {
			return currentSlide
		}
	}

	function callSlidersMove(score1, score2) {
		setTimeout(() => {
			instanceRef1.current.moveToIdx(score1)
			instanceRef2.current.moveToIdx(score2)
		}, 100)
	}

	const getTiebreakPoints = useCallback((currentTiebreakScore, neighborTiebreakScore, setIndex) => {
		if (
			tiebreak && currentTiebreakScore === null && neighborTiebreakScore === null
			&& setIndex === activeSetIndex && tier === T1
		) {
			return 0
		} else if (!tiebreak && currentTiebreakScore === null && neighborTiebreakScore === null
			&& setIndex === activeSetIndex) {
			return ''
		} else if (currentTiebreakScore === 0 && neighborTiebreakScore === 0 && setIndex !== activeSetIndex) {
			return ''
		} else if (
			neighborTiebreakScore === null && setIndex === activeSetIndex
			&& currentTiebreakScore === null && tier === T2
		) {
			return 7
		} else if (
			neighborTiebreakScore && setIndex === activeSetIndex && currentTiebreakScore === null && tier === T1
		) {
			return 0
		} else if (
			neighborTiebreakScore && setIndex === activeSetIndex && currentTiebreakScore === null && tier === T2
		) {
			return 7
		} else if (
			neighborTiebreakScore && setIndex !== activeSetIndex && currentTiebreakScore === null && tier === T2
		) {
			return 7
		} else if (
			neighborTiebreakScore && setIndex !== activeSetIndex && currentTiebreakScore === null && tier === T1
		) {
			return 0
		} else {
			return currentTiebreakScore
		}
	}, [activeSetIndex, tiebreak, tier])

	const handleSavePopupScores = useCallback(() => {
		const updatedMatch = { ...matchState }
		const sets = [...updatedMatch.sets]
		let activeSet = getActiveSet(updatedMatch.sets, activeSetIndex)

		if (tiebreak) {
			activeSet = {
				...activeSet,
				tie_break_score1: Number(firstInput),
				tie_break_score2: Number(secondInput)
			}
		} else {
			activeSet = {
				...activeSet,
				score1: Number(firstInput),
				score2: Number(secondInput)
			}
		}

		sets[activeSetIndex] = activeSet
		updatedMatch.sets = sets
		setMatchState(updatedMatch)
		setPopupOpened(false)
	}, [activeSetIndex, tiebreak, matchState, firstInput, secondInput])

	const openPopup = useCallback((index) => {
		const updatedMatch = matchState

		const activeSet = getActiveSet(updatedMatch.sets, index)
		const score1 = activeSet.score1
		const score2 = activeSet.score2
		const tiebreak1 = activeSet.tie_break_score1
		const tiebreak2 = activeSet.tie_break_score2

		if (!tiebreak) {
			setFirstInput(score1)
			setSecondInput(score2)
		} else {
			setFirstInput(tiebreak1 ? tiebreak1 : 0)
			setSecondInput(tiebreak2 ? tiebreak2 : 0)
		}

		setPopupOpened(true)
	}, [matchState, tiebreak])

	const closePopup = useCallback(() => {
		setPopupOpened(false)
	}, [])

	const handleSetClick = useCallback((index) => {
		if (index === activeSetIndex) {
			openPopup(index)
		} else {
			setActiveSetIndex(index)
			changeSlidesInSwiper(matchState.sets, index, tiebreak)
		}
	}, [activeSetIndex, openPopup, changeSlidesInSwiper, matchState, tiebreak])

	const activateTiebreak = useCallback(() => {
		const updatedTiebreak = !tiebreak
		setTiebreak(prev => !prev)
		changeSlidesInSwiper(matchState.sets, activeSetIndex, updatedTiebreak)
	}, [changeSlidesInSwiper, matchState, activeSetIndex, tiebreak])

	function getProcessedTiebreak(sets) {
		return sets.map((set, index) => {
			const { tie_break_score1, tie_break_score2 } = set

			if (tie_break_score1 === null && tie_break_score2) {
				if (tier === T1) {
					set.tie_break_score1 = 0
				} else {
					set.tie_break_score1 = 7
				}
				return set
			} else if (tie_break_score2 === null && tie_break_score1) {
				if (tier === T1) {
					set.tie_break_score2 = 0
				} else {
					set.tie_break_score2 = 7
				}
				return set
			} else if (tie_break_score1 === 0 && tie_break_score2 === 0) {
				set.tie_break_score1 = null
				set.tie_break_score2 = null
				return set
			} else if (tie_break_score1 === null && tie_break_score2 === null
				&& tier === T2 && index === activeSetIndex && tiebreak) {
				set.tie_break_score1 = 7
				set.tie_break_score2 = 7
				return set
			} else {
				return set
			}
		})
	}

	const handleAddSet = useCallback(() => {
		const newSet = tier === T1 ? newSetT1 : newSetT2
		const addedSet = {
			...newSet,
			'order_number': matchState.sets.length + 1,
		}

		const sets = [...matchState.sets, addedSet]
		setMatchState(prev => { return { ...prev, sets } })

		const lastSetIndex = sets.length - 1
		setActiveSetIndex(lastSetIndex)

		changeSlidesInSwiper(sets, lastSetIndex, tiebreak)
	}, [matchState, tier, changeSlidesInSwiper, tiebreak])

	const handleRemoveSet = useCallback((index) => {
		const sets = [...matchState.sets]
		sets.splice(index, 1)

		setMatchState(prev => { return { ...prev, sets } })

		const lastSetIndex = sets.length > 0 ? sets.length - 1 : 0
		setActiveSetIndex(lastSetIndex)
		changeSlidesInSwiper(sets, lastSetIndex, tiebreak)
	}, [matchState, tiebreak, changeSlidesInSwiper])

	const handleSaveSet = async () => {
		if (sliderIsMoving) return

		const matchData = { ...JSON.parse(matchSession) }
		let sets = invertSets(matchState.sets, matchState.inverted)
		sets = getProcessedTiebreak(sets)
		matchData.sets = matchState.sets
		sessionStorage.setItem('match', JSON.stringify(matchData))

		const body = {
			'is_finished': false,
			sets
		}

		try {
			const response = await saveSet({ uid: matchState?.uid, body })

			if (!response?.error) {
				const sets = invertSets(response.data.sets, matchState.inverted)
				setMatchState({ ...matchState, sets })
			}
		}
		catch (e) {
			console.log('handleSaveSet catched error', e)
		}
	}

	const handleFinishMatch = async () => {
		if (sliderIsMoving) return

		let sets = invertSets(matchState.sets, matchState.inverted)
		sets = getProcessedTiebreak(sets)
		const body = {
			'is_finished': matchState.sets.length > 0 ? true : false,
			'sets': sets
		}

		try {
			const response = await updateMatch({ uid: matchState.uid, body })

			if (!response?.error) {
				sessionStorage.removeItem('match')
				navigate(-1)
			}
		} catch (e) {
			console.log('updateMatch catched error', e)
		}
	}

	function getInputScore(vaule) {
		let stringValue = String(vaule)
		if (stringValue.length <= 3) {
			return stringValue ? Number(vaule) : stringValue
		} else {
			stringValue = stringValue.slice(0, 3)
			return Number(stringValue)
		}
	}

	function exit() {
		navigate(matchState.pathBack)
	}

	const getSetStyle = useCallback((index) => {
		if (activeSetIndex === index) {
			return {
				borderRadius: '50px',
				border: '2px solid var(--palette-live-grey-6)'
			}
		} else {
			return {}
		}
	}, [activeSetIndex])

	const getTaibreakStyle = useCallback(() => {
		if (!tiebreak) {
			return {
				backgroundColor: 'var(--palette-live-bg-0-white)',
				border: '1px solid var(--palette-live-blue)',
				color: 'var(--palette-live-blue)'
			}
		} else {
			return {
				backgroundColor: 'var(--palette-live-blue)',
				border: '1px solid var(--palette-live-blue)',
				color: 'var(--palette-live-bg-0-white)'
			}
		}
	}, [tiebreak])

	if (!matchState && !matchSession) {
		return <Navigate to={getWayBack(location)} />
	}

	const showHeader = () => {
		if ((type === TYPE_DOUBLES && matchState.sets.length > 2)
			|| (type === TYPE_SINGLES && matchState.sets.length > 2)) {
			return false
		} else {
			return true
		}
	}

	return (
		<>
			<SmoothCollapse expanded={showHeader()}>
				<Header title={name} background />
			</SmoothCollapse>

			<div className="match__wrapper">
				<div className="match__container">

					<div className="match__container-top">
						<div className="match__participants">
							<div className="match__set-participant--first">
								{
									type === TYPE_SINGLES ?
										matchState?.side1.namePlayer
										:
										<div className="match__set-participant-double">
											<p>
												{matchState?.side1.namePlayer1}
											</p>
											<p>
												{matchState?.side1.namePlayer2}
											</p>
										</div>

								}
							</div>

							<div className="match__set-participant--second">
								{
									type === TYPE_SINGLES ?
										matchState?.side2.namePlayer
										:
										<div className="match__set-participant-double">
											<p>
												{matchState?.side2.namePlayer1}
											</p>
											<p>
												{matchState?.side2.namePlayer2}
											</p>
										</div>
								}
							</div>
						</div>

						{popupOpened &&
							<Popup title={tiebreak ? t('Tiebreak') : ''} leftTitle>
								<Input
									type={'number'}
									value={firstInput}
									onChange={(e) => setFirstInput(getInputScore(e.target.value))}
									name={'firstParticipantPoints'}
									placeholder={getNameForPlaceholder(matchState.side1, type)}
								/>

								<Input
									type={'number'}
									value={secondInput}
									onChange={(e) => setSecondInput(getInputScore(e.target.value))}
									name={'secondParticipantPoints'}
									placeholder={getNameForPlaceholder(matchState.side2, type)}
								/>

								<div className="match__popup-container">
									<div className="match__popup-button">
										<RoundedButton title={t('Cancel')} onClick={closePopup} border />
									</div>

									<div className="match__popup-button">
										<RoundedButton
											background={'white'}
											title={t('Save')}
											onClick={handleSavePopupScores}
											border
										/>
									</div>
								</div>
							</Popup>
						}

						<div className="match__sets">
							{matchState &&
								<>
									{
										matchState.sets.length > 0 &&
										matchState.sets.map((set, index) =>

											<div key={index} className="match__set-wrapper">
												{activeSetIndex === index &&
													<div className="match__set-nuber-container">
														<p className="match__set-nuber">{index + 1}</p>
													</div>
												}

												<div
													style={getSetStyle(index)}
													className="match__set-container"
													onClick={() => handleSetClick(index, matchState.sets)}
												>
													<div className={
														!tiebreak && activeSetIndex === index
															? 'match__set-score' : 'match__set-score--inactive'
													}
													>
														<p className="match__set-points">
															{set.score1}
														</p>
														<p
															className={
																tiebreak && activeSetIndex === index ?
																	'match__set-tiebreak' : 'match__set-tiebreak--inactive'
															}
														>
															{getTiebreakPoints(set.tie_break_score1, set.tie_break_score2, index)}
														</p>
													</div>

													<p>:</p>

													<div
														className={
															!tiebreak && activeSetIndex === index ?
																'match__set-score' : 'match__set-score--inactive'
														}
													>
														<p className="match__set-points">
															{set.score2}
														</p>

														<p
															className={
																tiebreak && activeSetIndex === index ?
																	'match__set-tiebreak' : 'match__set-tiebreak--inactive'
															}
														>
															{getTiebreakPoints(set.tie_break_score2, set.tie_break_score1, index)}
														</p>
													</div>
												</div>

												<div className="match__set-button-deletion">
													{activeSetIndex === index &&
														<button
															type="button"
															onClick={() => handleRemoveSet(index)}
														>
															<RemoveSet className="match__set-icon-deletion" />
														</button>
													}
												</div>

											</div>
										)
									}
								</>
							}

							{matchState && matchState.sets.length < 5 &&
								<button
									className="match__sets-button"
									onClick={handleAddSet}>
									{t('Add set')}
								</button>
							}
						</div>
					</div>

					<div className="match__sliders" >
						{loaded1 && instanceRef1.current && (
							<div className="keen-slider__buttons">
								<button
									onClick={(e) => {
										e.stopPropagation() || instanceRef1.current?.prev()
										setTouchedSlider(prev => { return { ...prev, left: true } })
									}}
									className="slider__button"
								>
									<Minus className="slider__button-icon" />
								</button>

								<button
									onClick={(e) => {
										e.stopPropagation() || instanceRef1.current?.next()
										setTouchedSlider(prev => { return { ...prev, left: true } })
									}}
									className="slider__button"
								>
									<Plus className="slider__button-icon" />
								</button>

							</div>
						)}

						<div className="sliders_container">
							<div ref={sliderRef1} className="keen-slider slider">
								{slidesArray.map((item, index) => (
									<div key={index} className="keen-slider__slide slider-slide">
										{item}
									</div>
								))}

								<div className="slider__container--above">
									<div className="slider__block-opacity-top"></div>
									<div className="slider__block-opacity-bottom"></div>
								</div>
							</div>

							<div className="sliders__separator"></div>

							<div ref={sliderRef2} className="keen-slider slider">
								{slidesArray.map((item, index) => (
									<div key={index} className="keen-slider__slide slider-slide">
										{item}
									</div>
								))}

								<div className="slider__container--above">
									<div className="slider__block-opacity-top"></div>
									<div className="slider__block-opacity-bottom"></div>
								</div>
							</div>
						</div>

						{loaded2 && instanceRef2.current && (
							<div className="keen-slider__buttons">
								<button
									onClick={(e) => {
										e.stopPropagation() || instanceRef2.current?.prev()
										setTouchedSlider(prev => { return { ...prev, right: true } })
									}}
									className="slider__button"
								>
									<Minus className="slider__button-icon" />
								</button>

								<button
									onClick={(e) => {
										e.stopPropagation() || instanceRef2.current?.next()
										setTouchedSlider(prev => { return { ...prev, right: true } })
									}}
									className="slider__button"
								>
									<Plus className="slider__button-icon" />
								</button>
							</div>
						)}
					</div>

					<div className="match__buttons">
						<button
							className="match__button-tiebreak"
							style={getTaibreakStyle()}
							onClick={activateTiebreak}
						>
							{t('Tiebreak')}
						</button>

						<DoubleButton
							typeSmall="button"
							onClickSmall={exit}
							arrow

							typeBig="button"
							onClickBig={handleSaveSet}
							titleBig={t('Save score')}
						/>

						<RoundedButton
							type="button"
							onClick={handleFinishMatch}
							title={t('Finish match')}
							background={'grey'}
						/>
					</div>
				</div>
			</div>
		</>
	)
}

export default memo(Match)