import { TYPE_SINGLES, TYPE_DOUBLES, NODE_TYPE_IN, ALPHABET } from '../../utils/constants'

import { getNewUid } from '../../utils/functions'

export function formLevels(levels, players, type, stageNumber) {
	let playersArray = players
	let counter = 0

	if (stageNumber === 1) {
		playersArray = players?.map((p, index) => {
			return { ...p, rating: index + 1 }
		})
	}

	const newLevels = levels?.map(lvl => {
		const newGroups = lvl?.groups?.map((grp, index) => {
			const nodes = grp?.nodes?.filter(node => node?.type === NODE_TYPE_IN)

			const formedParticipants = nodes.map((node) => {
				return getformedNode(playersArray, node, type)
			})

			const updateGroup = { ...grp, name: grp?.name || `Группа ${ALPHABET[counter]}`, nodes: formedParticipants }
			counter++

			return updateGroup
		})

		return { ...lvl, groups: newGroups }
	})

	return newLevels
}

export function formNodes(rating, players, type) {
	const initialRaiting = rating || createInitialRaiting(players)

	return initialRaiting?.map(node => {
		const nodeIsAccessible = { ...node, disabled: false }

		return getformedNode(players, nodeIsAccessible, type)
	}) || []
}

function createInitialRaiting(players) {
	return players?.map((p, index) => {
		return {
			bye: true,
			double_uid: p?.uid || null,
			name: index + 1,
			player_uid: p?.uid || null,
			uid: getNewUid(),
			type: 'incoming'
		}
	})
}

function getformedNode(players, node, type) {
	if (type === TYPE_SINGLES) {
		const participant = players?.find((p) => p.uid === node?.player_uid)
		let name = '---'
		let rating = null

		if (participant?.formedName) {
			name = participant?.formedName
			rating = participant?.rating || node?.name
		} else if (!node?.disabled && node?.bye) {
			rating = '-'
		}

		return {
			placed: true,
			nodeUid: node?.uid,
			bye: node?.bye,
			disabled: node?.disabled,
			type: node?.type,
			playerUid: participant?.uid || null,
			// groupName: grp.name,
			rating,
			name
		}
	} else if (type === TYPE_DOUBLES) {
		const participant = players?.find((p) => p.uid === node?.double_uid)
		let name = '---'
		let rating = null

		if (participant) {
			rating = participant?.rating ? participant?.rating : node?.name
			name = node?.name
		}else if ((!node?.disabled && node?.bye) || (!node?.disabled && !node?.bye && node?.double_uid)) {
			rating = '-'
		}

		return {
			placed: true,
			doubleUid: node?.double_uid,
			nodeUid: node?.uid,
			type: node?.type,
			bye: node?.bye,
			disabled: node?.disabled,
			rating,
			firstPlayerUid: participant?.player1?.uid || null,
			secondPlayerUid: participant?.player2?.uid || null,
			firstPlayerName: participant?.player1?.formedName || '',
			secondPlayerName: participant?.player2?.formedName || '',
			// groupName: grp.name,
			name
		}
	}
}

export function sortNodes(nodes) {
	const sortedNodes = nodes.sort((a, b) => a.rating - b.rating)

	return sortedNodes
}

export function definePlacedPlayers(levels, nodesList) {
	const involvedPlayers = []

	levels?.map(lvl => {
		lvl?.groups?.map(grp => {
			grp?.nodes?.map(n => {
				involvedPlayers.push(n)

				return n
			})

			return grp
		})

		return lvl
	})

	const placedPlayers = nodesList?.map(n => {
		const matchedPlayer = involvedPlayers?.find(
			p => (p?.playerUid && p?.playerUid === n?.playerUid) || (p?.doubleUid && p?.doubleUid === n?.doubleUid)
		)
		const placed = matchedPlayer ? true : false

		return { ...n, placed: placed }
	})

	return placedPlayers
}

export function dragItems(result, levels) {
	const [sourceLevelIndex, sourceGroupIndex] = result.source.droppableId.split('-')
	const [endLevelIndex, endGroupIndex] = result.destination.droppableId.split('-')

	const sourceGroup = { ...levels[sourceLevelIndex]?.groups[sourceGroupIndex] }
	const endGroup = { ...levels[endLevelIndex]?.groups[endGroupIndex] }

	const movedNode = {
		...levels[sourceLevelIndex]?.groups[sourceGroupIndex]?.nodes[result.source.index],
		groupName: endGroup?.name
	}
	const replacedNode = {
		...levels[endLevelIndex]?.groups[endGroupIndex]?.nodes[result.destination.index],
		groupName: sourceGroup?.name
	}

	return {
		sourceLevelIndex,
		endLevelIndex,
		endGroupIndex,
		sourceGroupIndex,
		endIndex: result.destination.index,
		sourceIndex: result.source.index,
		movedNode,
		replacedNode
	}
}

export function formNodesForRequest(stage, editedLevels, type) {
	const startNodes = []
	const editedNodes = []

	stage?.levels?.forEach(lvl => {
		lvl?.groups?.forEach(grp => {
			grp?.nodes?.forEach(n => {
				if (n?.type === NODE_TYPE_IN) {
					startNodes.push(n)
				}
			})
		})
	})

	editedLevels?.forEach(lvl => {
		lvl?.groups?.forEach(grp => {
			grp?.nodes?.forEach(n => {
				editedNodes.push(n)
			})
		})
	})

	const nodes = {
		nodes: []
	}

	startNodes?.forEach((startN, index) => {
		const editedNode = editedNodes[index]
		if (type === TYPE_DOUBLES) {
			nodes.nodes.push({ uid: startN.uid, double_uid: editedNode.doubleUid })
		} else {
			nodes.nodes.push({ uid: startN.uid, player_uid: editedNode.playerUid })
		}
	})

	return nodes
}

export function updateNodesList(levels, currentList) {
	const placedPlayers = []

	levels?.forEach(lvl => {
		lvl?.groups?.forEach(grp => {
			grp?.nodes?.forEach(n => {
				placedPlayers?.push({ ...n, groupName: grp?.name })
			})
		})
	})

	const updatedNodesList = currentList?.map(item => {
		const found = placedPlayers?.find(p => p?.nodeUid === item?.nodeUid)

		if (found) {
			return { ...item, groupName: found?.groupName }
		} else {
			return { ...item, groupName: null }
		}
	})

	return updatedNodesList
}

export function cleanNodesInGroups(levels) {
	return levels?.map(lvl => {
		return {
			...lvl,
			groups: lvl?.groups?.map(grp => {
				return {
					...grp,
					nodes: grp?.nodes?.map(n => ({
						...n,
						placed: false,
						name: '---',
						nodeUid: getNewUid(),
						playerUid: null,
						doubleUid: null,
						rating: '-',
						emptyPlayer: true,
					})),
				}
			}),
		}
	})
}