import { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'

import './Tournaments.scss'
import { useGetCommunityQuery } from '../../redux/communityService/communityApiSlice'
import Header from '../../reusableComponents/Header/Header'
import TournamentsList from '../../extendedComponents/TournamentsList/TournamentsList'
import AddButton from '../../reusableComponents/AddButton/AddButton'
import { ReactComponent as RocketsIcon } from '../../icons/rokets.svg'
import BottomMenu from '../../reusableComponents/BottomMenu/BottomMenu'
import Plug from '../../reusableComponents/Plug/Plug'
import Loader from '../../reusableComponents/Loader/Loader'

import { selectCommunities, selectCommunity } from '../../redux/selectors'

function Tournaments() {
	sessionStorage.clear()
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { communityUid, communityName } = useSelector(selectCommunity)
	const { communities } = useSelector(selectCommunities)
	const visibleTournaments = localStorage.getItem('visibleTournaments')
	const [isVisibleTournaments, setIsVisibleTournaments] = useState(!!JSON.parse(visibleTournaments) || false)

	const { data = {}, status, error } = useGetCommunityQuery(
		{ community_uid: communityUid }, { skip: !communityUid, refetchOnMountOrArgChange: true }
	)

	const [tournaments, setTournaments] = useState([])

	useEffect(() => {
		if (Object.keys(data).length !== 0) {
			setTournaments(data?.tournaments)
		}
	}, [data, dispatch])

	function displaySwitchTournaments() {
		setIsVisibleTournaments((prev) => {
			localStorage.setItem('visibleTournaments', JSON.stringify(!prev))

			return !prev
		})
	}

	if (error) { console.log(error) }

	return (
		<>
			<Header
				title={t('Tournaments')}
				communityName={communities?.length > 1 ? communityName : false}
			/>

			<main className="tournaments">
				{
					status !== 'fulfilled' ?
						<Loader />
						:
						<>
							{
								tournaments?.length < 1 ?
									<Plug
										icon={<RocketsIcon className="icon" />}
										title={t('Create tournament')}
										description={t('Click on the plus for creating a tournament')}
									/>
									:
									<>
										<button
											className="button__change-view"
											onClick={() => displaySwitchTournaments()}
										>
											{
												isVisibleTournaments ?
													'Упрощённый вид'
													:
													'Детальный вид'
											}
										</button>

										<TournamentsList
											list={tournaments}
											categories={data.categories}
											venues={data.venues}
											visible={isVisibleTournaments}
										/>
									</>
							}

							<AddButton
								to={'/form'}
							/>
						</>
				}
			</main>

			<BottomMenu />
		</>
	)
}

export default Tournaments