import { memo } from 'react'

import TournamentMenu from '../TournamentMenu/TournamentMenu'
import StagesMenu from '../StagesMenu/StagesMenu'

function CombineTournamentMenu({
	handlePen,
	handleStick,
	handleBuild,
	handleStageTab,
	handleAddNewStage,
	structure
}) {

	return (
		<TournamentMenu>
			<StagesMenu
				handlePen={handlePen}
				handleStick={handleStick}
				handleBuild={handleBuild}
				handleStageTab={handleStageTab}
				structure={structure}
				handleAddNewStage={handleAddNewStage}
			/>
		</TournamentMenu>
	)
}

export default memo(CombineTournamentMenu)