import { memo, useState, useCallback, useMemo } from 'react'
import { useTranslation } from 'react-i18next'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import SmoothCollapse from 'react-smooth-collapse'
import { Droppable } from 'react-beautiful-dnd'

import './StageGroup.scss'
import { useUpdateStageMutation } from '../../redux/stageService/stageApiSlice'
import { ReactComponent as ArrowDown } from '../../icons/arrow_down.svg'
import { ReactComponent as RoundRobinIcon } from '../../icons/round_robin.svg'
import { ReactComponent as StrucIcon } from '../../icons/struc.svg'
import { ReactComponent as EditPenIcon } from '../../icons/edit_pen.svg'
import { TYPE_DOUBLES, TYPE_SINGLES, GROUP_TYPE_OLYMPIC_PLUS } from '../../utils/constants'
import GroupPlayer from '../GroupPlayer/GroupPlayer'
import Popup from '../../reusableComponents/Popup/Popup'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'
import { selectStage, selectChosenPlayerInList, selectPlayersList, selectTournament } from '../../redux/selectors'
import { filterGroupsNodesInLevels, getParameters } from '../../utils/functions'
import {
	updateGroupNodes,
	setChosenPlayerInList,
	replaceParticipant,
	setStageData,
	addParticipant,
	updateGroupName
} from '../../redux/stageService/stageSlice'

import { addEmptyPlayerInList } from './externalFunctions'

function StageGroup({ levelIndex, groupIndex, edit }) {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { tournamentParams, stageNumber } = useParams()
	const { tournamentUid } = getParameters(tournamentParams)
	const [changeStageParameters] = useUpdateStageMutation()
	const stageState = useSelector(selectStage)
	const playersList = useSelector(selectPlayersList)
	const tournament = useSelector(selectTournament)
	const chosenPlayerInList = useSelector(selectChosenPlayerInList)
	const [hidenGroup, setHidenGroup] = useState(true)
	const [replacedPlayerIndex, setReplacedPlayerIndex] = useState('')
	const [popupIsOpened, setPopupIsOpened] = useState(false)
	const [openedGroupNameEdition, setOpenedGroupNameEdition] = useState(false)
	const levels = stageState?.levels

	const group = useMemo(() => {
		return levels[levelIndex]?.groups[groupIndex]
	}, [levels, levelIndex, groupIndex])

	const [groupName, setGroupName] = useState(group?.name)

	const popupList = useMemo(() => {
		return addEmptyPlayerInList(playersList, levels, levelIndex)
	}, [playersList, levels, levelIndex])

	const replacePlayer = useCallback((node, index) => {
		const clikedParticipantInGroup = levels[levelIndex].groups[groupIndex].nodes[index]

		if ((tournament.type === TYPE_DOUBLES && clikedParticipantInGroup.doubleUid === node.doubleUid)
			||
			(tournament.type === TYPE_SINGLES && clikedParticipantInGroup.playerUid === node.playerUid)) {
			return
		}

		const updatedLevels = filterGroupsNodesInLevels(levels, node, true)
		const group = { ...updatedLevels[levelIndex].groups[groupIndex] }
		updatedLevels[levelIndex].groups[groupIndex].nodes[index] = {
			...node,
			groupName: group?.name
		}

		dispatch(replaceParticipant(updatedLevels))
	}, [dispatch, groupIndex, levelIndex, levels, tournament.type])

	const openPopup = useCallback(() => {
		setPopupIsOpened(true)

		document.body.style.overflow = 'hidden'
	}, [])

	const closePopup = useCallback(() => {
		setPopupIsOpened(false)

		document.body.style.overflow = 'auto'
	}, [])

	const addPlayerFromPopupInGroup = useCallback((node) => {
		// include player in group and update redux statement
		const levelsWithFiltredNodes = filterGroupsNodesInLevels(levels, node)

		dispatch(setStageData({ ...stageState, levels: levelsWithFiltredNodes }))
		dispatch(addParticipant({
			levelIndex: levelIndex,
			groupIndex: groupIndex,
			node
		}))
	}, [dispatch, levelIndex, groupIndex, levels, stageState])

	const handleNameInPopup = useCallback((node) => {
		if (replacedPlayerIndex !== '') {
			replacePlayer(node, replacedPlayerIndex)

			setReplacedPlayerIndex('')
		} else {
			addPlayerFromPopupInGroup(node)
		}

		closePopup()
	}, [replacePlayer, addPlayerFromPopupInGroup, replacedPlayerIndex, closePopup])

	const addPlayerFromListInGroup = useCallback((index, node) => {
		const updatedLevels = levels.map(level => {
			return {
				...level,
				groups: level.groups.map(group => {
					return {
						...group,
						nodes: group.nodes.map(n => {
							if (tournament.type === TYPE_SINGLES) {
								if (n.playerUid === node.playerUid) {
									return chosenPlayerInList?.player
								} else if (n.playerUid === chosenPlayerInList?.player.playerUid) {
									return node
								} else {
									return n
								}
							} else {
								if (n.doubleUid === node.doubleUid) {
									return chosenPlayerInList?.player
								} else if (n.doubleUid === chosenPlayerInList?.player.doubleUid) {
									return node
								} else {
									return n
								}
							}
						})
					}
				})
			}
		})

		dispatch(updateGroupNodes({
			levels: updatedLevels
		}))

		dispatch(setChosenPlayerInList({}))
	}, [dispatch, levels, chosenPlayerInList?.player, tournament.type])


	const handleName = useCallback((index, node) => {
		const movePlayer = Object.keys(chosenPlayerInList).length !== 0
		const isOlympicPlus = group?.type === GROUP_TYPE_OLYMPIC_PLUS

		if (movePlayer && !isOlympicPlus) {
			addPlayerFromListInGroup(index, node)

			dispatch(setChosenPlayerInList({}))
		} else if ((isOlympicPlus) && movePlayer) {
			replacePlayer(chosenPlayerInList?.player, index)

			dispatch(setChosenPlayerInList({}))
		} else if (!node?.disabled) {
			setReplacedPlayerIndex(index)

			openPopup()
		} else {
			return
		}
	}, [addPlayerFromListInGroup, openPopup, chosenPlayerInList, replacePlayer, dispatch, group?.type])

	const handleInputEdition = useCallback((e) => {
		setGroupName(e.target.value)
	}, [])

	// Функция сохраняющая новое названия Группы
	const handleEditionDone = useCallback(async () => {
		dispatch(updateGroupName({
			levelIndex: levelIndex,
			groupIndex: groupIndex,
			groupName: groupName
		}))

		const updatedLevels = levels.map((level, lIndex) => {
			if (lIndex === levelIndex) {
				const updatedGroups = level.groups.map((group, gIndex) => {
					if (gIndex === groupIndex) {
						return { ...group, name: groupName }
					}

					return group
				})

				return { ...level, groups: updatedGroups }
			}

			return level
		})

		const body = { levels: updatedLevels }

		const response = await changeStageParameters({ body, tournament_uid: tournamentUid, stageNumber })
	}, [
		dispatch, levelIndex, groupIndex, groupName,
		levels, tournamentUid, changeStageParameters, stageNumber
	])

	// Функция вызывающая попап для смены названия Группы
	const handleButtonEdition = useCallback((name) => {
		if (name) {
			setGroupName(name)
		}

		setOpenedGroupNameEdition(true)
	}, [])

	// Функция вызывающая функцию которая сохраняет название группы и закрывает popup
	function saveChangeGroupName() {
		handleEditionDone()

		setOpenedGroupNameEdition(!openedGroupNameEdition)
	}

	// Функция возвращающая количество игроков в Группе в двух вариантах (4 или 3/4)
	function nodesLength() {
		const noNameNode = group?.nodes?.filter((node) => {
			return node?.name !== '---'
		})

		if (noNameNode?.length === group?.nodes?.length) {
			return group?.nodes?.length
		} else {
			return `${noNameNode?.length}/${group?.nodes?.length}`
		}
	}

	return (
		<>
			<div className="stage-group">
				<div className={`stage-group__headers ${!hidenGroup ? 'stage-group__headers--close' : ''}`}>
					<p className="stage-group__participants">
						{ nodesLength() }
					</p>

					<div className="stage-group__group-container">
						{
							group?.type === GROUP_TYPE_OLYMPIC_PLUS ?
								<StrucIcon className="struc-new__icon" />
								:
								<RoundRobinIcon className="round-robin__icon" />
						}

						<p className="stage-group__identify">
							{ groupName }
						</p>

						<EditPenIcon
							className="edit-pen__icon"
							onClick={() => handleButtonEdition(groupName)}
						/>
					</div>

					<ArrowDown
						className={`arrow-down__icon ${hidenGroup ? 'arrow--up' : 'arrow--down'}`}
						onClick={() => setHidenGroup(prev => !prev)}
					/>
				</div>

				<SmoothCollapse expanded={hidenGroup}>
					<Droppable
						key={groupIndex}
						droppableId={`${levelIndex}-${groupIndex}`}
					>
						{
							(provided) => (
								<div
									{...provided.droppableProps}
									ref={provided.innerRef}
								>
									{
										group?.nodes?.map((node, index) => (
											<GroupPlayer
												key={node?.nodeUid}
												handleName={handleName}
												node={node}
												position={[levelIndex, groupIndex, index]}
												group={group}
											/>
										))
									}

									<div style={{ display: 'none' }}>
										{ provided.placeholder }
									</div>
								</div>
							)
						}
					</Droppable>
				</SmoothCollapse>
			</div>

			{
				popupIsOpened &&
					<Popup
						onClose={closePopup}
						title={t('Participants')}
						close
					>
						{
							popupList?.map((node, index) =>
								<div
									key={index}
									className={
										`stage-group__popup-participants-item ${node?.placed ?
											'stage-group__popup-participants-item--selected'
											:
											''
										}`
									}
									onClick={() => handleNameInPopup(node)}
								>
									{
										tournament?.type === TYPE_SINGLES &&
											<>
												<p className="stage-group__popup-participants-name">
													({ node?.rating }) { node?.name }
												</p>

												<p className="stage-group__popup-participants-name">
													{ node?.groupName }
												</p>
											</>
									}

									{
										tournament?.type === TYPE_DOUBLES &&
											<div className="stage-group__popup-participants-doubles">
												<p className="stage-group__popup-doubles-rating">
													({ node?.rating })
												</p>

												{
													node?.doubleUid === null ?
														<p className="stage-group__popup-doubles-name">
															{ node?.name }
														</p>
														:
														<div>
															<p className="stage-group__popup-participants-name--first">
																{ node?.firstPlayerName }
															</p>

															<p className="stage-group__popup-participants-name--second">
																{ node?.secondPlayerName }
															</p>
														</div>
												}
											</div>
									}
								</div>
							)
						}
					</Popup>
			}

			{
				openedGroupNameEdition &&
					<Popup title="Смена названия группы">
						<input
							type="text"
							name="groupName"
							value={groupName}
							onChange={e => handleInputEdition(e)}
							maxLength="8"
							className="group-name__input"
						/>

						<div className="group-name__button-container">
							<RoundedButton
								title="Отмена"
								onClick={() => setOpenedGroupNameEdition(!openedGroupNameEdition)}
								background="white"
								border
							/>

							<RoundedButton
								title="Сохранить"
								onClick={() => saveChangeGroupName()}
								border
							/>
						</div>
					</Popup>
			}
		</>
	)
}

export default memo(StageGroup)