import React, { memo, useState, useEffect, useRef, useCallback, useMemo } from 'react'
import SmoothCollapse from 'react-smooth-collapse'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { useParams, useNavigate } from 'react-router-dom'
import { DragDropContext } from 'react-beautiful-dnd'

import './StageForm.scss'
import { ReactComponent as ArrowDownIcon } from '../../icons/arrow_down.svg'
import StageGroup from '../StageGroup/StageGroup'
import {
	selectPlayersList,
	selectTournament,
	selectStage,
	selectChosenPlayerInList
} from '../../redux/selectors'
import {
	useGetStageQuery,
	useRemoveStageMutation,
	useFillStageMutation,
	useFillAutoStageMutation,
	useClearStageMutation
} from '../../redux/stageService/stageApiSlice'
import { getParameters, checkEquality } from '../../utils/functions'
import {
	setPlayersList,
	setStageData,
	setDragState,
	dragParticipant,
	setChosenPlayerInList,
	setCurrentStageNumber
} from '../../redux/stageService/stageSlice'
import RoundedButton from '../../reusableComponents/RoundedButton/RoundedButton'
import DoubleButton from '../../reusableComponents/DoubleButton/DoubleButton'
import Popup from '../../reusableComponents/Popup/Popup'
import { TYPE_SINGLES, TYPE_DOUBLES } from '../../utils/constants'

import {
	formNodes,
	formLevels,
	definePlacedPlayers,
	dragItems,
	cleanNodesInGroups,
	formNodesForRequest
} from './externalFunctions'

function StageForm({ tournamentRefetch, auth, stage }) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const immutableLevels = useRef()
	// Уровень этапа
	const { levels } = useSelector(selectStage)
	// tournamentParams - получаем id турнира
	// stageNumber - получаем номер турнира
	const { tournamentParams, stageNumber } = useParams()
	// Функция удаления этапа
	const [removeStage] = useRemoveStageMutation()
	// Функция отправки данных на сервер
	const [fillStage] = useFillStageMutation()
	const [fillAutoStage] = useFillAutoStageMutation()
	const [fillClearStage] = useClearStageMutation()
	// useState показываем или скрываем список участников
	const [showMainMenu, setShowMainMenu] = useState(false)
	// useState говорит хотим ли мы удалить этап или нет (или нет?)
	const [removeStageConfirmation, setRemoveStageConfirmation] = useState(false)
	// Список участников
	const playersList = useSelector(selectPlayersList)
	// Получаем id турнира
	const { tournamentUid } = getParameters(tournamentParams)
	// type - тип турнира, дабл или сингл
	// doubles - участники в дабл турнире
	// players - участники в синг турнире
	// stages_count - номер этапа
	const { type, doubles, players, stages_count } = useSelector(selectTournament)
	const [levelsMemory, setLevelsMemory] = useState([])
	const chosenPlayerInList = useSelector(selectChosenPlayerInList)

	useEffect(() => {
		if (Object.keys(stage).length !== 0) {
			const { levels, rating_initial } = stage

			// Формируем уровень
			const formedLevels = formLevels(levels, players || doubles, type, stageNumber)
			// Формируем игроков
			const formedNodes = formNodes(rating_initial, players || doubles, type)

			dispatch(setPlayersList(formedNodes))
			const stageState = { ...stage, levels: formedLevels }
			dispatch(setStageData(stageState))
			immutableLevels.current = formedLevels

			setLevelsMemory(formedLevels)
		}
	}, [stage, stageNumber, type, doubles, players, dispatch])

	const isEquale = useMemo(() => {
		return checkEquality(immutableLevels.current, levels, true)
	}, [immutableLevels, levels])

	const onDragEnd = useCallback((result) => {
		dispatch(setDragState({}))

		if (!result.destination || checkEquality(result.destination, result.source)) {
			return
		}

		const [endLevelIndex, endGroupIndex] = result.destination.droppableId.split('-')
		const { endIndex } = result.destination
		const replacedNode = { ...levels[endLevelIndex]?.groups[endGroupIndex]?.nodes[endIndex] }

		if (replacedNode?.disabled) {
			return
		}

		const object = dragItems(result, levels)
		dispatch(dragParticipant(object))
	}, [levels, dispatch])

	// Функция очищающая группы
	async function cleanGroups() {
		try {
			const response = await fillClearStage({
				tournament_uid: tournamentUid,
				stageNumber: stageNumber
			}).unwrap()

			const clearGroups = cleanNodesInGroups(levels)
			const stageState = { ...stage, levels: clearGroups }
			dispatch(setStageData(stageState))

			if (response?.error) {
				console.log(`ClearStage return error: ${response?.error}`)
			}
		} catch (error) {
			console.log(`fillClearStage catch error: ${error}`)
		}
	}

	// Функция возвращающая группы по умолчанию
	function returnGroups() {
		const stageState = { ...stage, levels: levelsMemory }

		dispatch(setStageData(stageState))
	}

	// Функция удаления этапа
	const handleRemoveStage = useCallback(async () => {
		try {
			let response = {}

			for (let i = stageNumber - 1; i < stages_count; i++) {
				response = await removeStage({
					tournament_uid: tournamentUid
				}).unwrap()

				if (response?.error) {
					console.log(`Remove stage return error: ${response?.error}`)
				}
			}

			tournamentRefetch()

			dispatch(setPlayersList([]))
			dispatch(setChosenPlayerInList({}))
			setRemoveStageConfirmation(false)

			if (response.stages_count > 0) {
				navigate(`/${tournamentUid}/structure/${response.stages_count}`)
				dispatch(setCurrentStageNumber(response.stages_count))
			} else {
				dispatch(setCurrentStageNumber(0))
				navigate(`/${tournamentUid}/structure/0`)
			}
		} catch (error) {
			console.log(`HandleRemoveStage stage catch error ${error}`)
		}
	}, [tournamentUid, dispatch, stageNumber, stages_count, removeStage, tournamentRefetch, navigate])

	const handleSaveFormedStage = useCallback(() => {
		try {
			const readyNodes = formNodesForRequest(stage, levels, type)

			const response = fillStage({
				tournament_uid: tournamentUid,
				stageNumber: stageNumber,
				body: readyNodes
			}).unwrap()

			if (response?.error) {
				console.log(`FormStage return error: ${response?.error}`)
			}
		} catch (error) {
			console.log(`HandleSaveFormedStage catch error: ${error}`)
		}
	}, [stage, levels, type, stageNumber, tournamentUid, fillStage])

	const handlePlayerInParticipantsList = useCallback((n, index) => {
		if (Object.keys(chosenPlayerInList).length === 0) {
			dispatch(setChosenPlayerInList({ player: n, index: index }))
		} else {
			dispatch(setChosenPlayerInList({}))
		}
	}, [dispatch, chosenPlayerInList])

	const refillStageSubmit = (async (event) => {
		try {
			const response = await fillAutoStage({ tournament_uid: tournamentUid, stageNumber }).unwrap()
		}
		catch (e) {
			console.log('Authomatical creation in stageCreation catch error', e)
		}
	})

	return (
		<div className="stage-form">
			<div className="stage-form__wrapper">
				<div className="stage-form__header">
					<p className="stage-form__quantity">
						{playersList?.length}
					</p>

					<p className="stage-form__stage-number">
						Этап {stage?.order_number}
					</p>

					<ArrowDownIcon
						className={`arrow-down__icon ${showMainMenu ? 'arrow--up' : 'arrow--down'}`}
						onClick={() => {
							setShowMainMenu(prev => !prev)
							const placedPlayers = definePlacedPlayers(levels, playersList)
							dispatch(setPlayersList(placedPlayers))
						}}
					/>
				</div>

				<div className="stage-form__list-participants">
					<SmoothCollapse expanded={showMainMenu}>
						{
							playersList?.map((item, index) =>
								<React.Fragment key={index}>
									{
										type === TYPE_SINGLES &&
										<React.Fragment key={index}>
											{
												item?.playerUid ?
													<div
														className="stage-form__list-participants-container"
														key={index}
														style={{
															color: !item?.placed ?
																'var(--palette-live-blue)'
																:
																'var(--palette-live-grey-0)',
															borderTopColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderRightColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderBottomColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'var(--palette-live-grey-11)',
															borderLeftColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
														}}
														onClick={() => handlePlayerInParticipantsList(item, index)}
													>
														<p className="stage-form__list-participants-index">
															{index + 1}
														</p>

														<div>
															<p className="stage-form__list-participants-name">
																{item?.name}
															</p>
														</div>

														<p className="stage-form__list-participants-rating">
															[{item?.rating}]
														</p>
													</div>
													:
													<div
														className="stage-form__list-participants-container"
														key={index}
														style={{
															color: !item?.placed ?
																'var(--palette-live-blue)'
																:
																'var(--palette-live-grey-0)',
															borderTopColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderRightColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderBottomColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'var(--palette-live-grey-11)',
															borderLeftColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
														}}
														onClick={() => handlePlayerInParticipantsList(item, index)}
													>
														<p className="stage-form__list-participants-name">
															{item?.name}
														</p>
													</div>
											}
										</React.Fragment>
									}

									{
										type === TYPE_DOUBLES &&
										<React.Fragment key={index}>
											{
												item?.doubleUid ?
													<div
														className="stage-form__list-participants-container"
														key={index}
														style={{
															color: !item?.placed ?
																'var(--palette-live-blue)'
																:
																'var(--palette-live-grey-0)',
															borderTopColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderRightColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderBottomColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'var(--palette-live-grey-11)',
															borderLeftColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
														}}
														onClick={() => handlePlayerInParticipantsList(item, index)}
													>
														<p className="stage-form__list-participants-index">
															{index + 1}
														</p>

														<div>
															<p className="stage-form__list-participants-name">
																{item?.firstPlayerName}
															</p>

															<p className="stage-form__list-participants-name">
																{item?.secondPlayerName}
															</p>
														</div>

														<p className="stage-form__list-participants-rating">
															[{item?.rating}]
														</p>
													</div>
													:
													<div
														className="stage-form__list-participants-container"
														key={index}
														style={{
															color: !item?.placed ?
																'var(--palette-live-blue)'
																:
																'var(--palette-live-grey-0)',
															borderTopColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderRightColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
															borderBottomColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'var(--palette-live-grey-11)',
															borderLeftColor: index === chosenPlayerInList.index ? 'var(--palette-live-blue)' : 'transparent',
														}}
														onClick={() => handlePlayerInParticipantsList(item, index)}
													>
														<div>
															<p className="stage-form__list-participants-name">
																{item?.name}
															</p>
														</div>
													</div>
											}
										</React.Fragment>
									}
								</React.Fragment>
							)
						}
					</SmoothCollapse>
				</div>

				<DragDropContext
					onDragEnd={(result) => onDragEnd(result)}
					onDragUpdate={(result) => dispatch(setDragState(result))}
					onBeforeDragStart={(result) => dispatch(setDragState(result))}
				>
					{
						levels?.length > 0 &&
						levels?.map((level, levelIndex) =>
							<div
								key={levelIndex}
								className="stage-form__level"
							>
								{
									levels?.length > 1 &&
									<div className="stage-form__level-wrapper">
										<p className="stage-form__level-name">
											{t('Level')} {level?.order_number}
										</p>
									</div>
								}

								{
									level?.groups?.map((group, groupIndex) =>
										<React.Fragment key={groupIndex}>
											<StageGroup
												levelIndex={levelIndex}
												groupIndex={groupIndex}
											/>
										</React.Fragment>
									)
								}
							</div>
						)
					}
				</DragDropContext>

				{
					levels?.length !== 0 &&
					<div className="stage-form__button-bottom">
						<RoundedButton
							title="Очистить"
							background="grey"
							onClick={cleanGroups}
							marginBottom
						/>

						<RoundedButton
							title="Перезаполнить"
							background="grey"
							onClick={() => refillStageSubmit()}
						/>
					</div>
				}

				{
					!isEquale && levels?.length > 0 &&
					<DoubleButton
						titleBig="Сохранить"
						arrow
						typeSmall={'button'}
						typeBig={'button'}
						onClickBig={() => handleSaveFormedStage()}
						onClickSmall={() => returnGroups()}
						fixed
					/>
				}
			</div>

			<button
				className="stage-form__delete-button"
				onClick={() => setRemoveStageConfirmation(true)}
			>
				{t('Remove stage')}
			</button>

			{
				removeStageConfirmation &&
				<Popup title="Это нельзя будет отменить">
					<p className="stage-form__confirmation">
						{t('Are you sure you want to remove stage?')}
					</p>

					<div className="stage-form__confirmation-buttons">
						<RoundedButton
							title={t('Cancel')}
							onClick={() => setRemoveStageConfirmation(false)}
							background="white"
							border
						/>

						<RoundedButton
							title={t('Remove')}
							onClick={handleRemoveStage}
							border
						/>
					</div>
				</Popup>
			}
		</div>
	)
}

export default memo(StageForm)