import React from 'react'
import { useTranslation } from 'react-i18next'
import { useParams, useNavigate } from 'react-router-dom'

import './NoContent.scss'
import { ReactComponent as StructureIcon } from '../../icons/structure.svg'
import { ReactComponent as RoundPlusIcon } from '../../icons/round-plus.svg'
import { ReactComponent as MagicStickIcon } from '../../icons/magic_stick.svg'

function NoContent({ table, generate, auth, structure, stageGeneration }) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const { stageNumber, tournamentParams } = useParams()

	return (
		<div className="no-content">
			{
				structure ?
					<p className="no-content__title">
						Создайте этап
					</p>
					:
					table &&
					<p className="no-content__title">
						{t(`There are no ${table} yet`)}
					</p>
			}


			{
				auth &&
				<div className="no-content__wrapper">
					{
						!stageGeneration ?
							generate === 'create' ?
								<p className={`no-content__description ${!structure && !table ? 'no-content__description--margin' : ''}`}>
									{
										structure ?
											<>
												Нажмите на плюс для создания этапа

												<RoundPlusIcon className="structure-icon" />
											</>
											:
											<>
												{t('Add stage in the structure tab')}

												<StructureIcon
													className="structure-icon"
													onClick={() => navigate(`/${tournamentParams}/structure/${stageNumber}`)}
												/>
											</>
									}
								</p>
								:
								generate === 'generate' &&
								<p className={`no-content__description ${!structure && !table ? 'no-content__description--margin' : ''}`}>
									{t('Generate stage in the structure tab')}

									<StructureIcon
										className="structure-icon"
										onClick={() => navigate(`/${tournamentParams}/structure/${stageNumber}`)}
									/>
								</p>
							:
							<p className={`no-content__description ${!structure || !table ? 'no-content__description--margin' : ''}`}>
								Сгенерируйте этап нажав на волшебную палочку

								<MagicStickIcon
									className="structure-icon"
									onClick={() => navigate(`/${tournamentParams}/structure/generation/${stageNumber}`)}
								/>
							</p>
					}
				</div>
			}
		</div>
	)
}

export default NoContent