import { memo } from 'react'
import { useTranslation } from 'react-i18next'

import './Popup.scss'
import { ReactComponent as CloseIcon } from '../../icons/old_icon/close.svg'

function Popup({ title, onClose, children, close, leftTitle }) {
	const { t } = useTranslation()

	return (
		<div className="popup">
			<div className="popup__wrapper">
				<div
					className="popup__header"
					style={{ justifyContent: close ? 'space-between' : leftTitle ? 'flex-start' : 'center' }}
				>
					<h2 className="popup__title">
						{ t(title) }
					</h2>

					{
						close &&
							<CloseIcon
								onClick={onClose}
								className="popup__close-icon"
							/>
					}
				</div>
				
				<div className="popup__body">
					{ children }
				</div>
			</div>
		</div>
	)
}

export default memo(Popup)